export const TAB =[
    /* 0 TOTAL : */"<i><small>Comment fait-on pour dtruire ?<br>On entre dans la vie de quelqu'un, on s'y immisce doucement, on s'y rend indispensable.<br>Puis, on dtruit tout en prtendant tre le sauveur.<br>Car, avec les jolies fleurs du greenwashing, on pense cacher l'immense emprise de l'industrie fossile.<br><br>Total s'est glisse au coeur de tous les secteurs de la socit pour s'assurer une influence sans faille.<br>Culture, sport, universits, grandes coles, centres de recherches, think tanks,<br>banques, assurances, lobbies, associations environnementales, sociales et surtout l'Etat.<br><br>Le travail d'enqute est infini. Les liens que l'entreprise tisse sont innombrables.<br>Personne ne doit perturber sa trajectoire en direction des sols.<br><br>Car Total continue,<br>envers et contre tous les avis scientifiques,<br>de forer massivement les sols et les ocans  la recherche de ptrole et de gaz.<br><br>Les recommandations du GIEC et de l'Agence Internationale de l'Energie sont pourtant claires: <br>Il faut abandonner<br>ds maintenant<br>tout nouveau projet d'exploitation d'nergie fossile.<br><br>Mais Total s'immisce partout o l'on pourrait l'empcher,<br>partout o elle peut crer de la dpendance.<br>Discrte mais omniprsente,<br>elle se rend indispensable et inattaquable.<br><br>Cette stratgie d'influence ne vise qu'un seul objectif : la rentabilit<br>Mga-profits<br>Bnfices nets de 19,5 milliards d'euros<br>Gant franais, tache d'huile immense<br>Parmi une infustrie fossile super puissante, mais pas infailible.<br><br>Emptrs dans des liens plus ou moins troits avec Total, <br>tous les acteurs de cette toile se garderont bien de se prononcer contre les actions de l'entreprise.<br>Ces informations sont publiques, existent aux yeux de tous grce au travail des journalistes et associations,<br>mais dans la rsignation gnrale,<br>Total entretient son influence malsaine.<br><br>Pourtant, notre capacit  contraindre les industries fossiles dans leur course effrne<br>dtermine aujourd'hui<br>non seulement l'avenir,<br>mais la survie de l'humanit.<br><br>Comment fait-on pour arrter de dtruire ?<br>Un geste : <br>rompre les liens.<br><br></i>                                   Jade Verda</small>",
    "<b>Les actionnaires</b> <br /><br />En 2021, le groupe a consacre plus de 8,5 milliards deuros a la remuneration de ses actionnaires, soit pres de trois fois plus que les sommes investies dans le renouvelable. Total appartient a ses actionnaires, repete souvent Patrick Pouyanne pour justifier des orientations strategiques du groupe. Qui sont donc ces fameux actionnaires, qui decident du sort du groupe et font rente de ses benefices ? Ce sont principalement des geants financiers (BlackRock, le Credit Agricole...) et des acteurs etrangers (seuls 6,9 % des actionnaires principaux sont immatricules en France). Par ailleurs fortement relies au monde des banques et des assurances, leur interet nest que financier.",
    "<b>Le groupe Credit Agricole</b> <br /><br />Le Groupe Credit Agricole est le premier actionnaire de Total au monde : il rassemble pour 15,66 milliards deuros dactions Total, soit 10,2% du groupe. Cette concentration sopere en grande partie via Amundi, la filiale du Credit Agricole deuxieme societe de gestion dactifs en Europe, qui gere entre autres le Plan dEpargne Entreprise du groupe. Le 29 mai 2020, lorsque lassemblee generale de Total a fait lobjet dun vote au sujet de ladoption ou non dune resolution climat, premiere de ce genre en France, le Credit Agricole a fait partie des 83,2% des actionnaires qui ont vote contre la resolution. Selon lassociation Reclaim Finance, les engagements annonces par le Credit Agricole ne se sont pas traduits en actes, le groupe preferant la perspective dune hausse a court terme des dividendes.",
    "<b>BlackRock</b> <br /><br />Premiere societe gestionnaire dactifs au monde, BlackRock est le second actionnaire de Total avec 6,2 % des parts, pour une valeur de 9,49 milliards deuros. En janvier 2021, les ONG Reclaim Finance et Urgewald ont qualifie de greenwashing les annonces de BlackRock et son PDG Laurence D. Fink, qui ont notamment souhaite se positionner comme champion de la transition en arretant tout investissement dans une entreprise realisant plus de 25% de son activite a partir des energies fossiles, mais maintiennent pour 85 milliards de dollars de participations dans des compagnies inscrites sur la Global Coal Exit List assemblee par lONG Urgewald.",
    "<b>Vanguard Group</b> <br /><br />Le groupe Vanguard est un groupe de conseil en investissement base aux Etats-Unis, tout comme le premier actionnaire BlackRock, et possede 3,5 % de Total. De fait, plus de trois quarts des actionnaires principaux de Total sont des societes de gestion dactifs, qui ont pour but de decouper les parts du groupe Total et de les associer a dautres actions afin de constituer des produits financiers et obtenir un rendement sur ces produits. Leur objectif est donc uniquement et tres directement le rendement financier, et leur possession de parts de Total extremement diluee dans le marche financier.",
    "<b>Norges Bank Investment Management</b> <br /><br />Ce fonds souverain gere directement par la Banque Centrale de Norvege detient pour 5,07 milliards deuros dactions chez Total, soit 3,31% de lactionnariat. Cest le principal fonds souverain, cest-a-dire detenu par un acteur public, au monde. En 2019, les autorites publiques norvegiennes ont approuve le debut de lexploitation par Total du projet Johan Sverdrup, qui vise a extraire 2,7 milliards de barils pendant 50 ans. La NBIM nest pas le seul actionnaire public de Total ; la Caisse de Depot et Placement du Quebec detient par exemple pour plus de 500 millions deuros dactions du groupe.",
    "<b>BNP Paribas</b> <br /><br />BNP Paribas est avec le Credit Agricole le seul groupe francais des 20 principaux actionnaires de Total : elle en detient pour 1,36 milliards deuros dactions, ce qui represente 0,89% du capital total du groupe. Lorsquen 2020 une minorite dactionnaires a initie le vote dune resolution climat, BNP Paribas sest positionnee la veille en co-signant une lettre demandant a Total de revoir a la hausse ses engagements. Cependant, cet engagement a ete critique par certaines associations comme une forme de greenwashing, alors que BNP Paribas a augmente ses financements a lindustrie fossile entre 2017 et 2020 malgre ladoption en 2017 dune politique sur les petrole et gaz non conventionnels.",
    "<b>Natixis</b> <br /><br />Natixis, etablissement financier majeur en France issu de la fusion de la Banque Populaire et de la Caisse dEpargne, est le 29eme actionnaire de Total dans le monde pour une valeur de 376 millions deuros. Natixis, comme le Credit Agricole et BNP Paribas, a fait partie des 83,2% des actionnaires qui ont vote contre la resolution climat proposee par une minorite dactionnaires de Total a lassemblee generale du 29 mai 2020.",
    "<b>Axa</b> <br /><br />Le groupe assurantiel Axa, pouvant assurer ou non les projets de Total, detient pour 117,05 millions deuros dactions de Total ; cest une pratique courante, le groupe assurantiel Allianz est par exemple le trentieme actionnaire de Total avec 368 millions deuros dactions Total. Lorsque le 29 mai 2020, lassemblee generale de Total fait lobjet dun vote au sujet de ladoption ou non dune resolution climat, premiere de ce genre en France, Axa fait partie des 83,2% des actionnaires qui ont vote contre la resolution. Par ailleurs, Patricia Barbizet, administratrice de Total depuis plus de dix ans, est egalement administratrice dAxa.",
    "<b>Membres du Conseil dadministration de Total</b> <br /><br />La majorite des membres du Conseil dadministration, ceux-la memes qui votent les orientations strategiques du groupe, ont des actions chez Total. Ainsi, Patrick Pouyanne detient pour 15,6 millions deuros dactions chez Total, Patricia Barbizet pour 645 000  et Jerome Contamine pour 600 000 . A ce titre, ils ont tous beneficie en 2022 dune augmentation de 42 % du dividende annuel recu.",
    "<b>Le conseil d'admnistration</b> <br /><br />Le Conseil dadministration de Total est lorgane decisionnel du groupe, il enterine ses orientations strategiques, comme la strategie climat. Ses membres ne sont pas des cadres de Total mais des administrateurs qui cumulent souvent dautres fonctions : PDG de banques et grands groupes, administrateurs dentreprises du CAC 40, presidents ou administrateurs decoles dingenieurs, dinstitutions culturelles, dassociations ou cercles dinfluences, etc. La pratique de lentre-soi dans les conseils dadministrations des grands groupes est profondement ancree en France, chacun se nommant tour a tour tel un jeu de chaises musicales. En effet, les dirigeants du CAC 40 francais sorganisent de maniere a defendre de maniere coherente et solidaire leurs interets communs sur les questions importantes, notamment en matiere de fiscalite ou de climat. Ces liens sont renforces par le cercle de sociabilite des elites, comme par exemple via le club prive Le Siecle (qui a ete dirige par Patricia Barbizet, administratrice de Total). Ainsi, Total rassemble des administrateurs cles qui servent a tisser son influence sur la societe francaise et a linternational, grace a leurs liens professionnels et personnels.",
    "<b>Patrick Pouyanne</b> <br /><br />Patrick Pouyanne est le visage de Total. Apres y avoir pantoufle, il na jamais quitte le groupe. Il occupe dautres fonctions representatives par ailleurs, qui visent a accroitre son influence et ameliorer son image. <br />Issu du corps des Mines a la suite de sa formation a Polytechnique, Patrick Pouyanne a commence sa carriere au sein du ministere de lIndustrie. Il est ensuite devenu conseiller technique du Premier ministre Edouard Balladur en 1993, puis directeur de cabinet du ministre des Technologies de linformation et de la Poste Francois Fillon. Cest en 1997 quil rejoint la compagnie petroliere Elf Aquitaine, absorbee par le groupe Total en 2000. Patrick Pouyanne est un exemple parmi dautres, nombreux chez Total, de pantouflage, cest-a-dire le fait quun haut fonctionnaire parte travailler dans une entreprise privee. Patrick Pouyanne fait partie de cercles liant les grands patrons francais, il est notamment membre du conseil dadministration de l <b>Association Fran- caise des Entreprises Privees</b> depuis 2014, une association de representation patronale qui reunit uniquement les dirigeants des plus grands groupes considerant que leurs interets peuvent diverger des positions du Medef. <br />Il sest approche du monde des grandes ecoles ces dernieres annees, en integrant le conseil dadministration de l <b>Ecole Polytechnique</b> et de l <b>Institut Polytechnique</b> au moment ou il souhaitait imposer linstallation dun site Total en plein centre du campus. <br />Il dispose dune forte influence dans le monde associatif comme membre du conseil dadministration de la <b>Fondation La France sengage</b>, presidee par Francois Hollande, une fondation dont Total a activement participe a la creation en 2017 pour jouer un role de structure chapeau de nombreuses asso- ciations. <br />Il soigne egalement son image en siegeant au conseil dadministration de lInstitut du Monde Arabe, une institution culturelle specialisee sur cette zone geographique riche en hydrocarbures financee par la Fondation Total. <br /> Patrick Pouyanne a ete choisi en 2019 comme co-president de la <b>Partnering Against Corruption Initiative</b>, alors meme que le groupe a ete condamne pour de telles pratiques par le passe. Total a par exemple ete condamne en 2018 par le Tribunal correctionnel de Paris pour avoir verse 30 millions de pots- de-vin en Iran entre 2000 et 2004. <br />Il est meme president de l <b>Association Francaise des Entreprises pour lEnvironnement</b> depuis 2022. Cette association est loutil de communication de grandes entreprises francaises sur les sujets environnementaux. Elle a pour lun de ses trois buts principaux dameliorer la credibilite de ses entreprises en matiere denvironnement et regroupe une soixantaine de grandes entreprises francaises dont la plupart ont ete epinglees pour leur inconsequence a legard du dereglement climatique. <br />",
    "<b>Patricia Barbizet</b> <br /><br />Plus ancienne membre du conseil dadministration de Total, Patricia Barbizet est une femme daffaires ultra-connectee avec les cercles de socialisation des elites francaises. <br />Celle qui a ete bras droit de Francois Pinault puis directrice generale du <b>Groupe Pinault</b> jusquen 2018 a occupe la presidence de 2017 a 2020 du <b>club Le Siecle</b>, qui rassemble des dirigeants politiques, economiques, culturels et mediatiques francais pour des diners. En 2020, il comptait 766 membres et invites. Une fois par an, Patricia Barbizet organisait egalement un diner chez elle, rassemblant personnalites politiques et du monde des affaires. Patricia Barbizet participe egalement au <b>Club Bilderberg</b>, un evenement annuel et informel qui rassemble environ 130 personnes ultra-influentes : chefs dEtats, personnalites politiques, daffaires, des medias et diplomates europeens et americains. Son manque de transparence, la confidentialite des conferences qui sy tiennent et lalimentation de possibles conflits dinterets entre figures politiques et dirigeants des multinationales suscitent reguliere- ment de nombreuses polemiques. Patricia Barbizet y a participe au moins en 2016 et 2019. <br />Elle dispose dimportantes connexions au sein des grands groupes francais, notamment en cumulant un nombre impressionnant de postes dadminis- tratrice, comme a Axa </b> (qui detient pour 97 millions deuros dactions de Total), <b>Temaris</b> dont elle est presidente du conseil dadministration, <b>Pernod Ricard</b>, ou encore <b>Colombus Holdings</b>. Elle a aussi ete par le passe administratrice du groupe <b>Darty</b>, de <b>Kering</b> ou encore d<b>Agefi</b>. <br />Son role de chef de proue au sein de lecosysteme des grands groupes se distingue notamment par sa qualite de presidente du <b>Haut Comite de gouver- nance dentreprise de la place de Paris</b>, qui sassure que les entreprises cotees a la bourse de Paris respectent le code de mission fixe par lAssociation Francaise des Entreprises Privees et le Medef. <br />Elle nest pas etrangere non plus au secteur public, et compte parmi ses casquettes la presidence du <b>Comite de surveillance des investissements dave- nir</b>, une organisation publique rattachee au gouvernement. Ce comite, compose de 18 membres dont la directrice R&D de Total <b>Marie-Noel Seme- ria</b>, a pour mission declairer et de conseiller le gouvernement dans lelaboration et la conduite des politiques dinnovation. <br />Enfin, son influence large sillustre par son role de presidente du conseil dadministration de la <b>Cite de la Musique  Philharmonie de Paris</b>, dont elle a ete nommee par le Conseil des ministres en 2016. Elle a egalement ete membre du conseil dadministration de l<b>Opera de Paris</b> plusieurs annees.",
    "<b>Jean Lemierre</b> <br /><br />Jean Lemierre connecte Total au secteur de la finance et aux institutions publiques nationales et internationales. <br />Il est president du conseil dadministration de <b>BNP Paribas</b> depuis 2014 (groupe dont il etait conseiller du president des 2008). BNP Paribas de- tient 1,27 milliards deuros dactions chez Total et un autre membre du conseil dadministration de Total, Jacques Aschenbroich, est egalement membre du conseil dadministration de cette banque. Il dispose de connexions etendues dans le secteur de la finance, notamment comme membre de lInstitute of International Finance. Il participe surtout a la representation de la bourse de Paris a legard des institutions publiques et privees en tant que vice-president de lAssociation Paris Europlace (la bourse de Paris) aux cotes dYves Perrier, le directeur general dAmundi. <br />Membre du conseil dadministration de l<b>Association Francaise des Entreprises Privees</b>, le lobby des plus grands groupes francais, et du conseil international dOrange, Jean Lemierre fait partie de la sphere des grands patrons francais. <br />Jean Lemierre a eu une longue carriere dans le public avant de basculer dans le prive, cest donc un exemple de la pratique au plus haut niveau du pan- touflage. Dans l<b>administration francaise</b>, il a ete chef du Service de la Legislation fiscale, Directeur General des Impots, puis Directeur du Tresor en 1995. Il a ensuite integre le <b>gouvernement</b> en tant que directeur de cabinet du ministre de lEconomie et des Finances. Il a ensuite rejoint des institutions publiques internationales, notamment comme membre du Comite monetaire europeen de 1995 a 1998, president du <b>Comite economique et financier de lUnion europeenne</b> et president du Club de Paris, un groupe informel de creanciers publics dont le role est de trouver des solutions coor- donnees aux difficultes de paiement des pays endettes, de 1999-2000. De 2000 a 2008, il a ete president de la <b>Banque europeenne pour la reconstruction et le developpement</b>, une organisation internationale vouee au developpement de leconomie de marche en Europe de lEst a la suite de la guerre froide. Les connexions de Jean Lemierre avec le secteur public persistent aujourdhui, notamment par son role de president du <b>Centre dEtudes Prospectives et dInformations Internationales (CEPII)</b>, un centre detudes francais public et rattache au Premier ministre. Les publications du CEPII visent a orienter la decision publique, notamment dans le secteur de la transition energetique. Elles persistent aussi a linternational, puisque Jean Lemierre est par exemple encore membre du panel de conseil international de la banque centrale de Singapour.",
    "<b>Jacques Aschenbroich</b> <br /><br />Jacques Aschenbroich connecte Total au secteur de lindustrie dans lequel il est dirigeant, mais aussi au secteur des banques ou encore des grandes ecoles. <br />Il est PDG de <b>Valeo</b>, equipementier automobile pesant plus de 17 milliards deuros de chiffre daffaires. Issu du corps des Mines, il a mene une carriere dans le public avant dentrer dans le prive ; il a notamment ete conseiller technique pour les affaires industrielles au cabinet du Premier ministre Jacques Chirac entre 1987 et 1988. <br />Jacques Aschenbroich est en effet connecte au monde de lindustrie, notamment en etant aujourdhui administrateur de <b>Veolia Environnement</b>, mais aussi par le fait davoir ete longtemps PDG de <b>Saint-Gobain vitrage</b>. Il est aussi administrateur de BNP Paribas, la deuxieme banque fran- caise financant le plus le groupe et dont le conseil dadministration est preside par Jean Lemierre, egalement au conseil dadministration de Total. Notons ici une double-connexion avec Pierre de Chalendar, president actuel de Saint-Gobain et egalement au conseil dadministration de <b>BNP Paribas</b>. Jacques Aschenbroich occupe egalement le poste central de president du conseil dadministration de l<b>Ecole des mines de Paris</b>. <br />Il dispose de connexions dans le secteur public aussi par sa participation au reseau des <b>Conseillers du commerce exterieur de la France</b>, qui ras- semble des dirigeants francais dans lobjectif de conseiller les institutions publiques et favoriser les entreprises francaises a linternational.",
    "<b>Marie-Christine Coisne-Roquette</b> <br /><br />Issue de la riche famille industrielle du nord Coisne et Lambert (4 milliards deuros de fortune estimee), la dirigeante Marie-Christine Coisne-Roquette est l<b>administratrice referente de Total</b>, cest-a-dire quelle represente le conseil dadministration et en dirige les travaux. <br />Cette femme daffaires est tres connectee au monde du CAC 40 en tant que presidente et ancienne directrice generale de <b>Sonepar</b>, deuxieme groupe prive francais avec plus de 19 milliards deuros de chiffre daffaires. Elle est aussi administratrice du Medef International. Sonepar est specialise dans lelectronique, un champ recoupant parfois les activites de Total. Une enquete de lAutorite de la concurrence est en cours sur ce groupe pour la pratique coordonnee de prix deroges avec des partenaires, notamment Schneider, fournisseur du projet EACOP de Total. <br />Marie-Christine Coisne-Roquette est egalement vice-presidente du conseil dadministration de l<b>Association Nationale des Societes par Actions</b>. A lAssemblee generale de Total du 28 mai 2021, lorsque quelques actionnaires minoritaires setaient prononces pour un engagement plus fort de Total en matiere de politique climatique, Marie-Christine Coisne-Roquette setait appuyee sur la position de lANSA pour suggerer que les actionnaires netaient pas legalement en mesure de deposer une resolution sur le climat dans le contexte de cette AG. <br />Entre 2010 et 2015, deja administratrice de Total, Marie-Christine Coisne-Roquette a ete representante des entreprises privees et membre de la section Environnement au <b>Conseil Economique, Social et Environnemental</b>, qui est est la troisieme assemblee constitutionnelle de la Republique avec lAssemblee nationale et le Senat.",
    "<b>Maria van der Hoeven</b> <br /><br />Ancienne directrice executive de l<b>Agence internationale de lenergie</b>, Maria Van der Hoeven est une personnalite influente cle pour Total : elle lui ouvre les portes de nombreuses institutions multilaterales et illustre par sa dimension internationale et sa participation a des initiatives en faveur de la transition le role de vitrine du groupe joue par le conseil dadministration. <br />Maria van der Hoeven est une femme politique neerlandaise, qui a notamment ete parlementaire, <b>ministre</b> de lEducation, de la Culture et de la Science de 2002 a 2007 puis ministre des Affaires Economiques de 2007 a 2010 aux <b>Pays-Bas</b>. En matiere de regulation europeenne, les Pays- Bas constituent souvent un point de blocage. <br />Maria van der Hoeven a par la suite ete directrice executive de l<b>Agence internationale de lenergie</b> de septembre 2011 a aout 2015. Cette agence autonome de lOCDE fournit des rapports et des scenarios sur le secteur de lenergie qui font autorite : Total retient par exemple, pour le calcul des de- preciations de ses actifs oil & gas Amont, un des scenarios de lAIE. <br />De 2016 a 2018, elle a ete vice-presidente du <b>Groupe dexperts de haut niveau de lEuropean Decarbonisation Pathways Initiative au sein de la Com- mission europeenne</b>, alors quelle etait deja administratrice de Total a cette epoque. Depuis mai 2021 elle est egalement <b>Secretaire generale du Conseil mondial de lenergie</b> et conseillere speciale sur la maitrise de lenergie. <br />Enfin, elle est membre de lorganisation <b>Leaders pour la Paix</b>, une ONG rassemblant des anciens ou actuels chefs dEtats, ministres et diplomates avec pour but de promouvoir la paix dans la diplomatie internationale et dans lespace public.",
    "<b>Mark Cutifani</b> <br /><br />Mark Cutifani connecte Total avec le secteur minier. <br />Il est administrateur et Chief Executive d<b>Anglo American Platinum</b>, lune des plus grandes entreprises minieres au monde qui fournit par exemple 40% de la production mondiale de lithium. Personnage central de cette entreprise britannique apres y avoir realise lessentiel de sa carriere, Mark Cutifani exerce egalement par extension les roles de Chairman dAnglo American South Africa et de De Beers. <br />Il est lancien directeur des operations de la societe de production de nickel <b>Vale</b>. Il a egalement travaille dans dautres entreprises du secteur : Normandy Group, Sons of Gwalia, Western Mining Corporation, Kalgoorlie Consolidated Gold Mines et CRA.",
    "<b>Glenn Hubbard</b> <br /><br />Glenn Hubbard constitue la carte credibilite economique du conseil dadministration, et connecte par ce biais Total au monde de la recherche en eco- nomie, a des organismes de regulation de leconomie et meme a certains acteurs financiers dont le geant mondial <b>BlackRock</b>, second actionnaire de Total. <br />Economiste et universitaire americain, il est notamment professeur depuis 1988 a Columbia University et <b>doyen de la Columbia Business School</b> de 2004 a 2019. Il est egalement professeur associe pour la Kennedy School of Government, Harvard Business School, Universite de Chicago. <br />Il a occupe de nombreux hauts postes dans les domaines economiques et fiscaux des Etats-Unis : secretaire adjoint du gouvernement americain pour la politique fiscale en 1991, puis de 1993 a 2001 membre du Panel of Economic Advisors de la Federal Reserve Bank of New York. Membre de 2001 a 2003 du Council of Economic Advisers des Etats-Unis et Conseil economique national, Conseil de securite nationale, <b>Conseil presidentiel sur la science et la technologie de la Maison Blanche</b> au debut des annees 2000, et conseiller de la Federal Reserve Bank of New York de 2007 a 2017. Il a egalement ete president du Comite de politique economique de lOCDE. <br />Il est aujourdhui co-president du <b>Comite americain sur la reglementation des marches de capitaux</b>, un organisme de recherche sur la reglemen- tation de la finance. <br />Par ailleurs, Glenn Hubbard siege au conseil dadministration dentreprises de marketing (<b>Dex Media</b>), de finance (KKR Financial Corporation), de services informatiques (<b>Automatic Data Processing</b>) et dassurance (<b>MetLife</b>). Il est surtout administrateur de <b>BlackRock Fixed Income Funds</b>, BlackRock etant la plus grande societe de gestion dactifs au monde et le second actionnaire de Total, une firme critiquee pour son maintien de plus de 80 milliards de dollars dactifs dans les energies fossiles, malgre des declarations dengagement renouvelees.",
    "<b>Anne-Marie Idrac</b> <br /><br />Enarque, <b>deux fois membre dun gouvernement</b> comme secretaire dEtat aux Transports puis comme secretaire dEtat au Commerce Exterieur, Anne-Marie Idrac dispose dun reseau significatif dans le public et au sein des grands groupes francais, notamment dans le secteur des transports. <br />De fait, elle a ete <b>PDG de la RATP</b> et de la <b>SNCF</b> au courant des annees 2000, et est encore aujourdhui <b>administratrice dAir France </b> et presidente de France Logistique, une organisation regroupant les acteurs du transport des marchandises et de la logistique qui participe aux travaux visant a faire evoluer la reglementation en matiere de transition energetique du transport routier de marchandises. Au sein du conseil dadministration dAir-France, Anne-Marie Idrac est par ailleurs presidente du Comite developpement durable et conformite. <br />Elle a egalement ete administratrice de <b>Bouygues</b> (notamment en tant que presidente du comite RSE), et est aujourdhui administratrice de <b>- Saint-Gobain</b> et de <b>Sanef</b>, la filiale francaise du groupe Abertis qui est numero un de la gestion dinfrastructures autoroutieres.",
    "<b>Jerome Contamine</b> <br /><br />Jerome Contamine connecte Total a la <b>Societe Generale</b> et a differents grands groupes francais.Jerome Contamine a ete embauche par <b>Elf Aquitaine</b>, rachetee par la suite par Total et Petrofina, dans les annees 1990. Il sortait alors de lENA et de quatre ans a la Cour des comptes. Il a par la suite occupe des postes de direction importants a <b>Veolia</b> et a <b>Sanofi</b>, qui lui ont ouvert les portes de plusieurs conseils dadministration. Ainsi, si Jerome Contamine est administrateur de Total, il est aussi administrateur de la Societe Generale, sur le podium des banques francaises financant le plus le groupe petrolier.",
    "<b>Lise Croteau</b> <br /><br />Lise Croteau connecte Total au secteur de la communication et surtout de lenergie au Canada (en renforcant sa vitrine multi-energies dans ce domaine). De fait, Lise Croteau a realise la majeure partie de sa carriere en tant que directrice financiere et vice-presidente d<b>Hydro-Quebec</b>, la societe publique en charge de la production, du transport et de la distribution de lelectricite au Quebec. Cela lui a notamment ouvert les portes du conseil dadministration de <b>Boralex</b>, une societe canadienne numero un en France sur leolien terrestre. En matiere de communication, Lise Croteau est membre du conseil dad- ministration de <b>Quebecor</b>, le chef de file canadien des telecommunications, du divertissement et des medias, un groupe concurrent des Desmarais, famille daffaires canadienne qui fut longtemps representee au conseil dadministration de Total.",
    "<b>Emma de Jonge</b> <br /><br />Emma de Jonge siege au conseil dadministration de Total en tant que representante des salaries actionnaires. <br />Elle a ete cadre chez <b>Elf Aquitaine</b> dans les annees 1990, avant que la firme ne soit rachetee par Total avec Petrofina, compagnie belge, pour creer la major que lon connait aujourdhui. Elf Aquitaine etait reputee pour exercer une influence sur des espaces geopolitiques instables et y exploiter les ressources fossiles presentes.",
    "<b>Romain Garcia-Ivaldi</b> <br /><br />Romain Garcia-Ivaldi siege au conseil dadministration de Total en tant que representant des salaries. Il est diplome de l<b>ENSTA Paris</b> et de l<b>IFP School</b>, lecole affiliee a lInstitut Francais du Petrole - Energies Nouvelles, un institut de recherche public, mais qui est pourtant directement liee a Total. Romain Garcia-Ivaldi a fait son alternance, puis toute sa carriere chez Total.",
    "<b>Angel Pobo</b> <br /><br />Angel Pobo siege au conseil dadministration de Total en tant que representant des salaries, une obligation introduite par la loi Pacte de 2019. Il a ete soutenu au moment de son election par la <b>CFDT</b>, dont il a cree une section en 2006 alors quil etait directeur dune station-service.",
    "<b>Banques</b> <br /><br />Les banques multiplient les declarations dengagement ecologique. Pourtant, elles continuent a financer Total qui depend delles pour ses nouveaux projets dexploitation des energies fossiles. Les quatre plus grosses banques francaises ont accorde plus de 16 milliards de dollars de financements a Total entre 2016 et 2020. Le Credit Agricole, egalement premier actionnaire de Total, est le premier financeur du groupe au monde depuis la signature de lAccord de Paris.",
    "<b>Le Credit Agricole</b> <br /><br />Le Credit Agricole est le premier financeur de Total depuis la signature des accords de Paris en 2016 : un financement a hauteur de 7,12 milliards deuros. En mai 2022 encore, le Credit Agricole a fait partie de 12 banques qui ont accorde un pret de 8 milliards de dollars a Total, avec JP Morgan, Barclays, BNP Paribas et la Societe Generale. Les connexions entre Total et le Credit Agricole sont nombreuses. Amundi, la societe de gestion dactifs liee au Credit Agricole, est le premier actionnaire de Total avec plus de 8 milliards deuros dactifs investis. Par ailleurs, Amundi gere le plan Epargne Entreprise du groupe. A lassemblee generale de Total du 28 mai 2021 ou la strategie climat de Total a ete adoptee, lobservatrice exterieure designee etait Caroline Le Meaux, une cadre dAmundi.",
    "<b>BNP Paribas</b> <br /><br />BNP Paribas a finance Total a hauteur de 5,86 Mds depuis 2016, alors meme que la banque detient pour 1,36 milliards deuros dactions dans le groupe, le deuxieme total pour une banque francaise. Les connexions entre la banque et le groupe sont egalement dordre personnel. Jean Lemierre est a la fois pre- sident du conseil dadministration de BNP Paribas et membre du conseil dadministration de Total, et Jacques Aschenbroich est egalement present aux deux conseils dadministration. Par ailleurs, BNP Paribas est attaquee en justice depuis octobre 2022 par OXFAM, Les Amis de la Terre et Notre Affaire a Tous pour non-respect du devoir de vigilance.",
    "<b>Societe Generale</b> <br /><br />La Societe Generale a finance Total a hauteur de 2,49 Mds depuis 2016. Par ailleurs, Jerome Contamine est au CA de Total et au CA de la Societe Generale.",
    "<b>Assurances</b> <br /><br />Des liens forts existent entre le secteur assurantiel et Total. Pour chaque projet, lentreprise sappuie sur des assureurs specifiques. Dans ce secteur opaque, certains assureurs sont meme des actionnaires de Total. Linformation au sujet des assurances nest pas publique, mais lorsque ces liens sont mis en lumiere et denonces par les militants ecologistes comme pour le projet EACOP, de nombreux assureurs se retirent et declarent publiquement ne pas soutenir le projet. On compte parmi ces assureurs les francais Axa et Scor mais aussi Munich Re, Zurich, Allianz, Swiss Re et Hannover Re. Mis en difficulte, le groupe Total a ete contraint de se tourner vers un courtier en assurance pour trouver de nouveaux assureurs pour ce projet petrolier. Par ailleurs, lentreprise couvre ses plus grands risques industriels par elle-meme, via sa propre societe de reassurance.",
    "<b>Omnium Reinsurance Company</b> <br /><br />LOmnium Reinsurance Company est la compagnie de reassurance interne au groupe Total, par laquelle Total sassure elle-meme contre la plupart des grands risques industriels. Total immatricule cette societe en Suisse.",
    "<b>Axa</b> <br /><br />Le secteur de lassurance demeure opaque : de nombreux groupes assurantiels nouent des contrats avec Total, projet par projet. Axa peut servir ici dexemple : appuyees sur des rapports dagence de conseil, plusieurs associations ont calcule quAxa se trouvait malgre son choix de ne pas assurer le projet Eacop parmi les quinze plus gros assureurs de lindustrie petroliere et gaziere. Par ailleurs, Axa detient pour 117 millions deuros dactions de Total et Patricia Bar- bizet, administratrice de Total depuis plus de dix ans, est egalement administratrice de Axa.",
    "<b>Allianz</b> <br /><br />Allianz peut etre citee en exemple dassurance ayant couvert un nouveau projet dexploitation dhydrocarbures de Total aux cotes de AAI (groupe Suncorp), AIG, XL (desormais groupe AXA), Chubb, HDI Global, Munich Re, SCOR, Sompo, Swiss Re, Tokio Marine, Zurich et cinq syndicats du Lloyds de Londres. Ces assurances ont toutes participe a la couverture assurantielle du projet Ichthys en Australie, un des plus importants projets gaziers mondiaux entre terre et mer, dont une expansion est en discussion afin de prolonger lextraction de gaz jusquen 2060.",
    "<b>Marsh McLennan</b> <br /><br />Marsh McLennan est le courtier en assurances charge par Total de trouver des assureurs au projet Eacop de pipeline chauffe en Ouganda et en Tanzanie. Avec un chiffre daffaires de plus de vingt milliards deuros, cette societe americaine de gestion de risques opere pour ce projet via sa filiale Guy Carpenter.",
    "<b>Universits & Ecoles</b> <br /><br />Des membres du Conseil dadministration et cadres de Total siegent dans la majorite des ecoles dingenieurs francaises, de nombreux salaries du groupe y sont egalement enseignants et Total finance directement de nombreuses institutions via des partenariats et donations. Les meilleures ecoles sont un vivier essentiel de recrutement des futurs salaries de Total, quils cherissent des leurs stages de fin detudes. Ces liens influencent egalement les directions deta- blissement dans la composition des programmes et contenus de cours, notamment sur les questions climatiques, dans la mesure ou ils dependent financie- rement de Total. Via la structure TotalEnergies professeurs associes, lentreprise a tisse un reseau de plus de 250 professeurs salaries ou retraites de Total, qui donnent plus de 600 interventions, notamment sur la transition energetique, dans des universites du monde entier.",
    "<b>Institut Polytechnique de Paris</b> <br /><br />Total est membre fondateur, mecene et collaborateur de l'Institut Polytechnique de Paris, un institut de sciences et de technologies de rang mondial regrou- pant cinq prestigieuses ecoles d'ingenieurs francaises : lEcole polytechnique, lENSTA Paris, lENSAE Paris, Telecom Paris et Telecom SudParis.",
    "<b>Polytechnique</b> <br /><br /><b>Patrick Pouyanne</b>, PDG de Total, siege au CA de Polytechnique depuis 2018 et TotalEnergies est partenaire de lecole. Le groupe avait pour projet dinstaller son centre de recherche et de developpement de plus de 10 000 metres carres et 400 employes au cur de lecole. Cette decision a provoque une mobilisation inedite detudiants et professeurs de Polytechnique, qui ont reussi a obtenir le renoncement de Total en 2021, apres deux ans de bataille. Une enquete preliminaire du Parquet National Financier a ete ouverte en avril 2021 contre Patrick Pouyanne, pour des faits susceptibles de constituer un delit de prise illegale dinterets. Par ailleurs, Total finance la chaire  Defis technologiques pour une energie responsable  de lecole.",
    "<b>ENSTA</b> <br /><br />La directrice du projet Total a Saclay, <b>Nathalie Brunelle</b>, siege au CA de lENSTA.",
    "<b>Telecom ParisTech</b> <br /><br />La directrice commerciale chez Total et qui a fait toute sa carriere dans lentreprise, <b>Sophie Vergne</b>, siege au Conseil decole de Telecom ParisTech.",
    "<b>Ecole des Mines Paris</b> <br /><br />Jacques Aschenbroich</b>, siege au CA de Total et est president du CA de lEcole des Mines de Paris. <b>Pascal Dauboin</b>, cadre de la recherche et innovation de Total, a ete enseignant de 1986 a 2016 a lEcole des Mines. Il a notamment ete directeur de la R&D de la filiale dexploration et de production de Total en Russie.",
    "<b>ESTP</b> <br /><br />Apres une longue carriere chez Total, <b>Benoit Luc</b> a ete Directeur Europe de la branche Marketing & Services de 2012 a 2020 et est ensuite devenu vice-president de lassociation TotalEnergies Professeurs Associes. Il est administrateur de lESTP depuis 2014, lEcole speciale des Travaux publics, du Ba- timent et de lIndustrie. Il delivre egalement un cours Climate Change and Energy Transition dans plusieurs grandes universites. Un autre cadre de Total, Christian Basse, ayant travaille dans les ressources humaines de lentreprise pendant plus de 25 ans, etait professeur de management interculturel a lESTP en 2021 et 2022.",
    "<b>Sciences Po</b> <br /><br />En 2021, Total a finance la Fondation Nationale des Sciences Politiques, qui assure la gestion de lecole, a hauteur de 50 000 . De nombreux professeurs des masters lies a lenergie sont des anciens ou actuels employes de Total. Par exemple, <b>Laurent David</b> est responsable de la strategie chez Total Gas, renewables and power est professeur a Sciences Po depuis 2017 et aux Ponts depuis 2008. En revanche, le partenariat strategique entre Total et Sciences Po, commence en 2011, a ete mis a son terme en 2022. Cest une victoire pour le Collectif Sciences Po Zero Fossiles qui milite depuis 2014 pour que Sciences Po mette un terme a ce partenariat.",
    "<b>HEC</b> <br /><br />Le directeur executif de la Chaire Energie et Finance de HEC depuis 13 ans est <b>Jean-Michel Gauthier</b>, egalement professeur associe a Polytechnique, un ancien cadre de Total. Il y explique la strategie energetique de Total pour preserver le climat en 2019. En decembre 2022, des etudiants dHEC ont bloque le stand de Total lors dun forum de recrutement organise par lecole, denoncant notamment le projet EACOP. Cest une premiere au cur de la plus grande ecole de commerce francaise.",
    "<b>IFP School</b> <br /><br />IFP School est le nom de marque de lEcole nationale superieure du petrole et des moteurs. LIFP School est directement liee a lIFP Energies nouvelles (suc- cesseur de lInstitut francais du petrole), un acteur majeur de la recherche et de la formation dans les domaines de lenergie, du transport et de lenvironne- ment. Un grand nombre de dirigeants de Total sont des anciens eleves de cette ecole. Par ailleurs, lIFP School est lie par des anciens eleves et enseignants au collectif TotalAssocies. Cest par exemple le cas de Gerard Glotin, ingenieur petrolier, et Bruno Caline, ingenieur geologue chez Total.",
    "<b>Universite Toulouse 3 Paul Sabatier</b> <br /><br />LUniversite Toulouse 3 Paul Sabatier ouvre en 2022, en partenariat avec Total, une formation axee sur la transition energetique.",
    "<b>ENSG</b> <br /><br />LENSG est lie par de nombreux anciens eleves et enseignants au collectif TotalAssocies. Par exemple Bruno Caline est Ingenieur-geologue chez Total depuis 1988, il donne des cours a lIFP School, a l ENSG et a lUPPA.",
    "<b>Universite de Tours</b> <br /><br />Le delegue Environnement TotalEnergies, <b>Jean-Paul Cazalets</b>, a donne de 2018 a 2020 une serie de cours dans le cadre de Masters 2 Urbanisme et Droit de lEnvironnement a lUniversite de Tours.",
    "<b>TotalEnergies Professeurs Associes</b> <br /><br />TotalEnergies Professeurs Associes est une association de plus de 250 professeurs adherents, salaries ou retraites de la Compagnie TotalEnergies, qui vise a promouvoir les relations entre le monde de leducation, le monde de lenergie et le groupe. Les professeurs membres realisent 610 interventions dans 26 pays selon le site internet de lassociation.",
    "<b>Universite Mohammed VI Polytechnique</b> <br /><br />Lancien directeur general de Total Oman, <b>Mohammed Zaki</b>, est aujourdhui directeur general de lUniversite Mohammed VI Polytechnique.",
    "<b>ENTPE, ecole de lamenagement durable des territoires</b> <br /><br />Des cours communs ont ete donnes sur lenvironnement dans le cadre dune formation conjointe IFP School/ENTPE par Jacques Mine, specialiste des etudes dimpact environnemental pour tout type de projet dexploration petroliere chez Total.",
    "<b>Columbia University</b> <br /><br />Glenn Hubbard</b>, membre du Conseil dAdministration de Total, est professeur a Columbia University depuis 1988 et doyen de la Columbia Business School de 2004 a 2019.",
    "<b>Planete Energies</b> <br /><br />En toute discretion, Total fournit du materiel pedagogique presentant les questions energetiques a des milliers denseignants de colleges et de lycees fran- cais. Planete Energies est le nouveau nom depuis 2021 de Total Solar Expert, la structure montee par Total qui a ainsi touche environ 500 000 eleves sur les dix dernieres annees. Se targuant de ne pas placer son logo sur le materiel pedagogique, Total cherche plutot a definir comment doit etre approchee la ques- tion energetique pour les plus jeunes sans que cela soit identifie. Cette initiative est par ailleurs partenaire avec lInstitut Francais du Petrole School, lecole directement liee a Total.",
    "<b>Recherche</b> <br /><br />Total dispose dune branche de recherche et developpement qui a porte de nombreuses innovations en matiere dexploitation des energies fossiles, notam- ment en collaboration avec la recherche publique alors que lEtat etait present au capital du groupe. Seulement aujourdhui, Total est une entreprise privee ou lEtat na plus aucune participation. Pourtant, les connexions entre Total et le monde de la recherche demeurent abondantes : une enquete menee par Green- peace a montre que plus de la moitie des 100 plus grandes structures publiques de recherche sont liees a Total. 14 chaires de recherche sont financees par Total, 16 laboratoires ont des programmes de recherche communs avec le groupe, 8 structures publiques de recherche comptent un membre de Total dans leur gouvernance. Une structure comme lInstitut Francais du Petrole, bien quil soit devenu lIFP Energies Nouvelles, continue de financer avec de largent public une recherche exclusivement monopolisee par la firme. Cest une strategie doccupation, alors que le budget R&D de TotalEnergies compte deja pour 1,1 milliard deuros soit le triple du budget du CNRS dans le domaine de lenergie ou presque autant que lensemble de la depense publique de recherche dans ce secteur. Ce phenomene doccupation se double dune strategie plus sournoise, mise en valeur par lenquete de Greenpeace : Total finance des secteurs pre- cis de la connaissance, comme limpact carbone du rayonnement solaire, afin de detourner lattention et la progression de la connaissance de limpact de la combustion des hydrocarbures. Ce double-jeu de participation vitrinesque et de financement oriente est une strategie du doute 2.0, la nouvelle etape deployee par le groupe apres avoir directement cherche a dissimuler le dereglement climatique au cours des annees 1990, comme lont montre plusieurs chercheurs francais en 2021.",
    "<b>Institut Francais du Petrole - Energies Nouvelles</b> <br /><br />Total est partenaire de cet institut de recherche public qui est lancien Institut Francais du Petrole, seulement renomme depuis IFP Energies Nouvelles. Cest un organisme de recherche particulier, qui est public (le president de son Conseil dAdministration est nomme par le Conseil des Ministres et redigent des rapports en collaboration avec le CNRS et le CEA) mais dont plus de 50% des financements proviennent de partenaires industriels dont Total. LIFPEN dispose egalement dune ecole, lIFP School, dont de nombreux professeurs sont des employes de Total et dont de nombreux eleves garnissent les rangs du groupe petrolier.",
    "<b>Campus Paris-Saclay</b> <br /><br />Total a directement cible dans sa strategie dinfluence le pole scientifique et technologique de Paris-Saclay qui concentre une grande partie des sites de for- mation et de recherche des ingenieurs francais. Selon une enquete de Greenpeace publiee en janvier 2023, 85 % des structures de recherche de Paris-Saclay specialisees dans le climat et la transition energetique sont liees a Total. Le groupe a directement finance des centres de recherche, notamment lEnergy4Cli- mate de lInstitut Polytechnique, dont il se positionne comme un fondateur direct et dont il participe a la gouvernance.",
    "<b>College de France</b> <br /><br />Total finance des chaires de recherche du College de France, a la fois pour ameliorer son image et pour orienter la recherche, notamment en soutenant Marc Fontenave, auteur du livre Halte au catastrophisme ! qui denonce un catastrophisme ecologique. De 2008 a 2016, Total a dabord ete partenaire de la chaire developpement durable du College de France. En 2019, Total a aussi signe un partenariat avec la chaire de Chimie des processus biologiques du Col- lege de France, dont est titulaire <b>Marc Fontecave</b>, auteur dinterventions remarquees au sujet du dereglement climatique (Si on admet, et je crois quil faut le faire, la theorie quil y a une relation mathematique  dailleurs avec des equations etablies par le GIEC qui valent ce quelles valent, demain ce sera surement une autre equation  eh bien oui, on connait nos emissions de CO2 donc on peut le traduire en temperature. Cest une theorie qui est developpee par le GIEC qui vaut ce quelle vaut, cest comme dans toute la science, on etablit des verites a un moment donne, certaines deviennent des verites, comme la Terre est ronde). Enfin, Total est grand mecene de la chaire Avenir commun durable du College de France, qui sinteresse precisement aux transitions environnementale et energetique.",
    "<b>Michel Aubier</b> <br /><br />Michel Aubier est un pneumologue employe par Total qui a minimise pendant plusieurs decennies les effets de la pollution de lair sur la sante. La justice, qui la condamne pour omission de declaration dactivite complementaire, a reconnu en 2017 un conflit dinterets qui durait depuis 1997. Ce pneumologue, an- cien chef de service a lhopital Bichat et professeur a luniversite, a realise de nombreuses etudes scientifiques et interventions dans les medias minimisant les consequences de la pollution de lair.",
    "<b>Philippe Drobinski</b> <br /><br />Le directeur du laboratoire climat de Polytechnique a monte en partenariat avec Total une chaire pour une energie responsable, que le groupe a finance a hauteur de 3,8 millions deuros. Disposant de fonctions etendues au sein de Polytechnique (charge de mission aupres de la direction sur les energies renou- velables, membre du conseil dadministration de lassociation des anciens eleves),<b>Philippe Drobinski</b> a ouvertement defendu limplantation de Total sur le campus, arguant de la necessite de collaborer avec le groupe alors meme que certaines de ses positions dependent des financements de ce dernier.",
    "<b>Universite de Pau et des Pays dAdour</b> <br /><br />Luniversite a proximite du centre des installations de R&D de Total a fait lobjet de financements et de tissages de partenariats nourris du groupe, si bien quelle en est aujourdhui directement dependante, comme la montre une enquete recente de Greenpeace. Les dons de Total atteignent 20 a 30 % des res- sources propres de luniversite, et les trois laboratoires cles de luniversite travaillent en partenariat avec Total.",
    "<b>Universite de Reims</b> <br /><br />Total finance a lUniversite de Reims un projet de mesure des emissions de gaz a effet de serre - notamment de methane. Lentreprise, accusee detre dans les plus grandes emettrices de gaz a effet de serre du monde, finance la recherche qui chiffrera ces emissions.",
    "<b>Global Biodiversity Information Facility</b> <br /><br />Total produit des donnees utilisees directement par la communaute scientifique depuis quelle a rejoint la plateforme de donnees Global Biodiversity Informa- tion Facility (GBIF). En 2021, les donnees chargees concernent les projets de la Compagnie en Afrique du Sud, Oman, Ouganda, Danemark et Argentine et ont fait lobjet de plus de 4 700 telechargements et dune quinzaine de citations dans des publications scientifiques.",
    "<b>Association des Instituts Carnots</b> <br /><br />Marie-Noelle SEMERIA, directrice R&D de Total, a ete presidente de lassociation des Instituts Carnots qui compte 30 laboratoires et centres de recherche francais. Elle a egalement ete membre du Centre de recherche Mines ParisTech, notamment apres avoir rejoint Total en 2017.",
    "<b>Museum National dHistoire naturelle</b> <br /><br />Le MNHN est un etablissement public de recherche, denseignement et de diffusion de la science dans le domaine des sciences naturelles. Dans sa mission de recherche, le MNHN sinteresse directement au recul masif du vivant. Dans sa mission de diffusion de la science, notamment par le biais dexpositions et de formations, le MNHN est amene a exposer les enjeux de la transition ecologique. En 2021, la Fondation Total a finance directement le MNHN a hauteur de 75 000, ainsi que son laboratoire de biologie des organismes et des ecosystemes aquatiques a hauteur de plus de 200 000.",
    "<b>Think Tanks</b> <br /><br />Total a recemment cible les think tanks dans sa strategie dinfluence, car ces derniers jouent un role important dans le faconnement de lopinion publique. De fait, les membres des think tanks jouent un role dexpert dans les medias et dans le debat public, que le groupe exploite pour faire passer les messages necessaires a la poursuite de son activite.",
    "<b>Institut francais des relations internationales</b> <br /><br />Total finance lIfri, premier centre de recherche et de debat sur les relations internationales francais, classe ces trois dernieres annees dans le trio de tete des think tanks les plus influents au monde par le Global Go To Think Tank Index Report de lUniversite de Pennsylvanie. Par ailleurs, Jean-Claude Mallet, directeur des affaires publiques de Total et ancien conseiller special du ministre des Affaires etrangeres, siege au Conseil Strategique de ce think tank.",
    "<b>IDees</b> <br /><br />Total finance IDees, un think thank qui reflechit sur les thematiques energetiques et fournit aux decideurs et pouvoirs publics leurs travaux pour les orienter dans la prise de decision. Ce think thank revendique un esprit de stricte neutralite mais travaille avec Total.",
    "<b>Le rameau</b> <br /><br />En 2021 Le Rameau recoit 530 000  de Total. Le Rameau est un laboratoire dinnovations partenariales, cest-a-dire un observatoire national et un centre dexperimentations sur les modeles de partenariats, pouvant proposer ses methodes comme retribution.",
    "<b>Laboratoire de la mobilite inclusive</b> <br /><br />Total a cree en 2012 ce laboratoire pour rassembler les acteurs de linclusion et de la mobilite - des acteurs publics participent aujourdhui a linitiative.",
    "<b>Institut Montaigne</b> <br /><br />Total est adherent a cet institut disposant dune forte reputation aupres des decideurs.",
    "<b>Mdias</b> <br /><br />Total ne participe pas directement a la concentration des medias qui a fait lobjet dune enquete parlementaire fin 2021. Cependant, Total emploie des ex- perts se presentant comme independants dans les medias et dispose de relations appuyees avec les plus grands cabinets de communication qui influencent lopinion publique.",
    "<b>France 5</b> <br /><br />En 2016, Mediapart avait revele que le remplacant occasionnel de Yves Calvi dans lemission publique C dans lair, <b>Thierry Guerrier</b>, etait employe par Total : cest un exemple de la pratique courante des  menages . Des journalistes reconnus mettent leur notoriete au service dune entreprise. Ainsi, Thierry Guerrier, journaliste depuis les annees 1980 et ancien directeur en chef dEurope 1, est embauche par Total a partir de 2013, pour produire un audit de sa stra- tegie de communication puis pour differentes interventions. Sur C dans lair, il a ete conduit a animer des debats parfois relatifs aux politiques energetiques.",
    "<b>Vae Solis Communication</b> <br /><br />Le president de ce cabinet de relations publiques <b>Philippe Maniere</b>, qui compte Total parmi ses clients, a ete tres actif pour decredibiliser la Conven- tion citoyenne pour le climat. Cest un exemple parmi dautres dexperts ou de chroniqueurs disposant a la fois dune audience dans lespace public et de connexions avec Total sans que ces deux elements soient relies pour les auditeurs.",
    "<b>Publicis</b> <br /><br />Publicis a accompagne Total dans sa premiere vaste campagne destinee a marteler son ambition multi-energies, juste avant la COP 21 de 2015 a Paris. Un clip et de nombreux contenus visaient a changer limage purement  petroliere  de la marque dans 21 pays, sur 80 chaines de television, et a travers un mil- liard dimpressions. Le briefing de lagence de communication revele un ciblage specifique des decideurs.",
    "<b>Very Press Trip</b> <br /><br />En 2013, les relations publiques de Total publient un mini-film de fin dannee qui se veut humoristique dans lequel ils assument pleinement de fournir direc- tement des elements de communication aux journalistes. Souhaitant montrer lenjeu de leur adaptation aux reseaux sociaux, les relations publiques de Total ecrivent par exemple les relations avec les journalistes etaient au beau fixe... Ils ne refusent jamais un bon pique-nique !. On voit aussi les equipes de Total fournir aux journalistes une machine a enfumer, un sac a blabla.",
    "<b>Sport</b> <br /><br />Les grands evenements sportifs representent des enjeux diplomatiques et dimage importants pour Total, notamment dans les pays et continents ou lentre- prise a de nombreuses activites. Lexemple le plus marquant est la Coupe dAfrique des Nations TotalEnergies, levenement sportif le plus populaire du conti- nent qui est tout simplement nomme TotalEnergies. Cette strategie de soft power a pour objectif, entre autres, de faire accepter des projets controverses par les populations des pays concernes.",
    "<b>Coupe dAfrique des Nations TotalEnergies</b> <br /><br />Sur le continent africain, ou le football est de loin le sport le plus populaire, Total a achete le sponsoring de la CAN pour la periode 2016-2024. Par le sponso- ring-titre de 1500 matchs en lespace de 8 ans, comprenant la CAN mais aussi plusieurs autres competitions dont la CAN feminine et la Ligue des Champions dAfrique, ce partenariat offre un affichage visuel massif a Total dans les stades et dans les medias. En 2021, la derniere CAN au Cameroun a enregistre plus dun milliard de vues sur TikTok et a atteint 50 millions de fans sur Facebook, dans un espace geographique qui concentre les projets controverses de Total, comme en Ouganda et Tanzanie, en Algerie, au Mozambique, en Afrique du Sud...",
    "<b>Coupe du monde de rugby 2023</b> <br /><br />Total est le sponsor officiel de levenement et beneficiera dune primaute sur lexposition medias et stades. Sur le terrain, Total va participer au train special France 2023 et aux Villages Rugby installes sur les places principales des neuf villes hotes. Les organisateurs de la Coupe du Monde 2023 reprennent les elements de langage de lentreprise et precisent que cet evenement sera loccasion pour TotalEnergies de mieux faire connaitre sa strategie multi-energies.",
    "<b>Section Paloise</b> <br /><br />Total est partenaire de cette equipe de rugby membre du Top 14, dont elle parraine notamment le centre de formation. Ce partenariat dure depuis 30 ans et sexplique en partie par lexploitation historique du gisement de gaz de Lacq, aujourdhui close.",
    "<b>Federation Internationale de badminton</b> <br /><br />Total est sponsor-titre de la Federation Internationale de Badminton, un statut acquis en 2015 contre plusieurs millions deuros. Cela lui confere une exposition sur les grands evenements de la Federation internationale de badminton, notamment les championnats du monde. Le vice-president marketing et services Asie-Pacifique de Total a justifie ce partenariat par lobjectif daccroissement de la presence du groupe dans la zone Asie-Pacifique, dou sont originaires 80 % des medaille.e.s olympiques de badminton.",

    "<b>DS-Techeetah</b> <br /><br />Total est partenaire depuis 2018 en Formule-E avec lecurie DS-Techeetah - la formule E est une competition de voitures a moteur electrique. Ce partenariat sinscrit dans une strategie dimage). Il sagit de prouver, en financant 13 equipes de sport automobile, que le groupe Total diversifie les types denergies dans lesquelles il investit (en loccurrence lelectrique).",
    "<b>24 heures du Mans</b> <br /><br />Total est le fournisseur officiel de carburant jusquen 2022 des 24 heures du Mans - et par ailleurs du Championnat du monde dEndurance dans son ensemble.",
    "<b>Opel e-Rally cup</b> <br /><br />Total est partenaire depuis le lancement en 2021 de cette nouvelle epreuve de rally. Il sagit de prouver, en financant 13 equipes de sport automobile, que le groupe Total diversifie les types denergies dans lesquelles il investit (en loccurrence lelectrique).",
    "<b>Team TotalEnergies cyclisme</b> <br /><br />Total est sponsor-titre et financeur principal depuis 2016 de cette equipe cycliste francaise, qui participe au Tour de France. Le groupe associe ainsi son image au velo, une pratique qui a lusage nemet pas demissions carbone.",
    "<b>Greenwashing</b> <br /><br />En 2021, Total devient TotalEnergies, fait peau-neuve, change de logo. Le groupe modifie la courte description de ses activites, dans un condense de greenwas- hing : TotalEnergies est une compagnie multi-energies mondiale de production et de fourniture denergies : petrole et biocarburants, gaz naturel et gaz verts, renouvelables et electricite. Sur six elements mentionnes, cinq paraissent verts. Pourtant, TotalEnergies, a date de 2021, cest une production de 257 unites dhydrocabures pour 1 unite denergies renouvelables.<br/>Sachant cela, le ton parait terriblement cynique. Et Total insisie : Presente dans plus de 130 pays, TotalEnergies inscrit le developpement durable dans toutes ses dimensions au cur de ses projets et operations pour contribuer au bien-etre des populations. Ce greenwashing est dangereux : les dirigeants de Total se posent comme partie de la solution, pour que lon oublie quils sont avant tout la source du probleme. Total est vise depuis decembre 2021 par une enquete du parquet de Nanterre pour  pratiques commerciales trompeuses , cest-a-dire pour greenwashing industriel. Cest une premiere en France.",
    "<b>Pacte Mondial des Nations Unies</b> <br /><br />En 2002, Total a rejoint le Pacte mondial des Nations unies, une initiative des Nations Unies pour inciter les entreprises a etre plus durables : cela permet a To- tal de beneficier du label des Nations Unies, organisation pourtant susceptible de remettre en question son activite. En 2020 par exemple, quatre rapporteurs speciaux de lONU avaient interpelle Total sur le projet EACOP de pipeline chauffe en Ouganda et en Tanzanie. La representation de lindustrie petroliere aux Nations Unies sest donne le nom dInternational Petroleum Industry Environmental Conservation Association.",
    "<b>Breakthrough Energy Coalition</b> <br /><br />Total est partenaire strategique de la Breakthrough Energy Coalition, un fonds cree par Bill Gates en 2016 pour investir dans la transition energetique. Le contenu concret de ce partenariat napparait pas dans les communications de Total, alors que ce fonds est compose uniquement dinvestisseurs individuels comme Jeff Bezos, fondateur dAmazon, ou Jack Ma, fondateur dAlibaba.",
    "<b>Association Francaise des Entreprises pour lEnvironnement</b> <br /><br />Lassociation francaise des Entreprises pour lEnvironnement est loutil de communication de grandes entreprises francaises sur les sujets environnementaux dont Patrick Pouyanne est devenu president en 2022. Elle a parmi ses trois buts principaux dameliorer la credibilite de ses entreprises en matiere denviron- nement et regroupe une soixantaine de grandes entreprises francaises, dont la plupart ont ete epinglees pour leur inconsequence a legard du dereglement climatique. Lassociation est un outil de communication mais aussi dinfluence pour ces entreprises : elle a par exemple co-organise en 2021 le European Business and Nature Summit avec la DG Environnement de la Commission europeenne. Surtout, en mai 2022, Patrick Pouyanne a ete elu president de las- sociation, un choix qui a suscite de vives reactions alors quil succedait deja a Jean-Laurent Bonnafe, alors DG de BNP Paribas, premier soutien europeen aux 100 entreprises developpant le plus denergies fossile avec 55.6 milliards de dollars accordes entre 2016 et 2021.",
    "<b>Roundtable on Sustainable Palm Oil</b> <br /><br />Total exige officiellement de ses fournisseurs quils soient adherents de la Roundtable on Sustainable Palm Oil : la RSPO est epinglee par cent ONG pour, sous couvert de volonte de protection de lenvironnement, freiner ladaptation du secteur de lhuile de palme et favoriser des pratiques de greenwashing.",
    "<b>Alliance to End Plastic Waste</b> <br /><br />TotalEnergies est membre fondateur de lAlliance to End Plastic Waste, qui reunit une quarantaine dentreprises de la chaine de valeur des plastiques dont Exon, Shell ou encore PepsiCo. Un rapport du think tank Planet Tracker AEPW - Barely credible a montre non seulement que les objectifs fixes par lAlliance etaient negligeables et non atteints, mais aussi que ses membres participaient en majorite a dautres organisations exercant des activites de lobby contre la regulation dans ce secteur.",
    "<b>Partnering Against Corruption Initiative</b> <br /><br />TotalEnergies a rejoint en 2016 la Partnering Against Corruption Initiative et ainsi adhere aux Principes de lutte contre la corruption de cette initiative, alors meme que le groupe a ete condamne pour de telles pratiques par le passe. Plus encore, le President-directeur general de TotalEnergies SE est devenu membre du Conseil du PACI en 2018 puis co-president de linitiative fin 2019. Total a par exemple ete condamnee en 2018 par le Tribunal correctionnel de Paris pour avoir verse 30 millions de pots-de-vin en Iran entre 2000 et 2004.",
    "<b>La fondation total</b> <br /><br />La Fondation Total est devenue un outil majeur de soft power, dinfluence culturelle et de diplomatie pour le groupe Total. Tous les financements accordes par la Fondation Total constituent de la publicite a moindre cout, puisque lEtat francais permet une deduction fiscale a hauteur de 60% des donations, et que Total peut avoir jusqua 25% de retributions de la part de lorganisme beneficiaire, par echange de services (ie. entrees gratuites aux expositions, mise a dispositions despaces, etc.). La Fondation Total finance de nombreux projets dont une partie est presentee ici et permet au groupe une presence diffuse par des dons dans des secteurs tres varies et les plus consensuels possibles : 19,8 millions deuros dans leducation et linsertion des jeunes (44%), 16,65 mil- lions deuros dans la culture et le patrimoine (37%), 5,4 millions deuros pour des initiatives liees au climat, aux littoraux ou aux oceans en 2020 (12%), 3,15 millions deuros pour la securite routiere (7%).",
    "<b>Associations environnementales</b> <br /><br />Le financement des associations dans le domaine du climat et de lenvironnement permet de canaliser de potentielles oppositions publiques aux actions menees par Total. En France, Total finance par exemple la reserve naturelle a proximite de sa principale raffinerie. Dans le monde, Total deploie des contribu- tions pour lutter contre lextreme-pauvrete, qui touche de nombreux territoires ou le groupe exploite des ressources pour son propre profit, et pour favoriser le reboisement, identifie comme une solution au rechauffement climatique dont les emissions des energies fossiles sont la cause.",
    "<b>World Food Programme</b> <br /><br />La Fondation Total consacre son don le plus important au Programme Alimentaire des Nations Unies : pres de 17,5 millions deuros en 2021.",
    "<b>Office National des Forets</b> <br /><br />La Fondation Total est un donateur historique de lONF, letablissement public charge de la gestion des forets. Depuis 2020, la Fondation Total fait partie des contributeurs au Fond Agir pour la foret, cree par lONF pour renforcer ses financements. Ainsi, en 2021, Total a contribue a hauteur de 240 000 a ce fonds, ainsi qua hauteur de 1 250 000 pour lun de ses projets partenaires A Tree for you visant a planter des arbres.",
    "<b>Museum National dHistoire Naturelle</b> <br /><br />Total finance le travail de recherche du Museum national dhistoire naturelle, mais finance aussi certaines de ses expositions, associant ainsi son image a la mise en avant de la nature qui y est faite. Le groupe est mecene depuis 1983 de la galerie de mineralogie et de geologie, et a par exemple finance en 2014 la renovation de cette derniere pour quelle puisse accueillir lexposition Tresors de la Terre.",
    "<b>Lassociation Francois-Xavier Bagnoud</b> <br /><br />La Fondation Total a finance en 2021 cette ONG de lutte contre lextreme-pauvrete dans le monde a hauteur de 460 000 , soit plus de 5% de son budget. Cette association dispose du statut consultatif special aupres du Conseil economique et social des Nations unies.",
    "<b>Agence Nationale de la Grande Muraille Verte</b> <br /><br />La Fondation Total finance depuis 2018 un observatoire du projet de Grande Muraille Verte, un projet de reforestation pour lutter contre la desertification porte par lUnion Africaine, lorganisation intergouvernementale des Etats africains.",
    "<b>Juste 2.0C</b> <br /><br />En 2021, la Fondation Total a finance une etude interdisciplinaire portant sur lempreinte humaine sur lenvironnement a hauteur de 50 000, avec pour nom de projet Juste 2.0 C. En 2015, Patrick Pouyanne avait revele dans une conference a Sciences Po tabler sur un scenario a 3 ou 3.5 degres pour 2100, au moment meme des discussions autour de lAccord de Paris, ce qui avait souleve de vives critiques.",
    "<b>Reserve Naturelle de lEstuaire de la Seine</b> <br /><br />La Fondation Total finance la reserve naturelle creee sur lestuaire ou se trouve leur plus importante raffinerie de France. Ce financement etait de seulement 27 500  en 2021, un montant qui semble faible face a lampleur de lactivite de la zone industrielle de Gonfreville-lOrcher. En 2022, une enquete a montre des niveaux importants de pollution de benzene et dhydrocarbures aromatiques.",
    "<b>Associations et social</b> <br /><br />Perpetuant une pratique courante au sein du CAC 40, Total associe son image a un certain nombre dassociations appreciees du grand public, sur des thema- tiques les plus consensuelles possibles. Pour cela, elle finance directement des associations comme la Croix rouge ou le Secours Populaire. Les donations sont pourtant de montants minimes comparativement au chiffre daffaires de deux cents milliards deuros du groupe, dautant quelles font lobjet dune de- duction fiscale de plus de soixante pourcents.",
    "<b>La Fondation La France Sengage</b> <br /><br />La Fondation La France Sengage a ete fondee par <b>Francois Hollande</b> en 2017. La Fondation Total en est le premier mecene (1 500 000  en 2021), puis vient la BNP. Patrick Pouyanne, PDG de Total, est egalement membre du Conseil dAdministration de La France Sengage depuis sa creation. Cette fon- dation represente un interet pour Total car elle regroupe plusieurs associations et joue ainsi un role de parapluie. Cest a la fois une instance de reseau car fondee par un ancien president de la Republique, mais egalement un elargissement des beneficiaires de largent de Total qui peuvent donc plus difficilement sopposer ouvertement aux actions de lentreprise.",
    "<b>La Croix Rouge Francaise</b> <br /><br />80 000  en 2021.",
    "<b>SNSM  Les sauveteurs en mer</b> <br /><br />De 2021 a 2025, le SNSM recoit 1 800 000 de la fondation Total.",
    "<b>Le Secours populaire francais</b> <br /><br />La Fondation Total finance le Secours populaire francais depuis 2009.",
    "<b>Portes tournantes</b> <br /><br />Les connexions entre Total et lEtat sont dordre personnel : de nombreux haut-fonctionnaires rejoignent Total en y apportant leur reseau et leurs connais- sances, et a linverse de nombreux cadres du groupe rejoignent lEtat a des postes de decision pouvant recouper les activites du groupe. Cette pratique, ap- pelee portes tournantes, permet a Total dacquerir un poids insidieux dans ladministration. Cest une pratique commune, mais pour laquelle Total est pas- see maitre en la matiere. Cest aussi pour Total un outil diplomatique lui permettant encore davantage de se substituer a lEtat. En 2014, lors de sa premiere rencontre avec Patrick Pouyanne, Vladimir Poutine declarait :  Total represente dune certaine maniere le pays lui-meme.",
    "<b>Jean-Claude Mallet</b> <br /><br />Jean-Claude Mallet est un haut-fonctionnaire issu de lENA qui a realise une carriere importante au sein de lEtat, dans le domaine de la defense. De 2012 a 2019, il a ete le conseiller special de <b>Jean-Yves Le Drian au ministere des Affaires etrangeres</b>, un poste cle lui donnant acces a un nombre significatif dinformations confidentielles sur les implications de la France a letranger ainsi qua lensemble du reseau diplomatique. En mai 2019, il est pourtant recrute par Total des sa fin de poste pour en devenir le directeur des affaires publiques. Selon le Courrier des Strateges, le transfert etonne certains observateurs et lhomme a des connexions avec le monde du renseignement et de la dissuasion nucleaire.",
    "<b>Helene Dantoine</b> <br /><br />La <b>Directrice de la diplomatie economique au ministere des Affaires Etrangeres</b> a travaille de 2011 a 2019 chez Total. Apres six ans comme diplomate et une formation a lENA conclue a lInspection Generale des Finances, grand corps dEtat, Helene Dantoine a ete de mai 2009 a novembre 2010 conseillere politique au cabinet du ministre des Affaires Etrangeres. Cest seulement un an apres cette experience qui lui a ouvert lensemble du reseau et des informa- tions propres a la diplomatie francaise quelle a rejoint Total, ou elle a acquis des responsabilites strategiques aux relations publiques internationales. Un an et demi seulement apres avoir quitte Total, Helene Dantoine effectue un nouvel aller-retour public/prive en devenant Directrice de la diplomatie economique au MEAE, un poste auquel elle favorise lactivite des groupes francais a linternational.",
    "<b>Ahlem Gharbi</b> <br /><br />Apres avoir travaille six ans au <b>Ministere des Affaires Etrangeres</b> en tant que specialiste sur la zone Afrique de Nord - Moyen-Orient, puis deuxieme secretaire a lambassade de France en Egypte et enfin conseillere a la representation permanente de la France aux Nations unies, elle est directement recru- tee par Total comme directrice adjointe des affaires internationales, en charge specialement des zones Afrique et Moyen-Orient ou son reseau et sa connais- sance des informations internes a lEtat est le plus developpe. Elle passe deux ans chez Total, puis devient <b>conseillere Afrique du Nord et Moyen-Orient du President Emmanuel Macron </b> entre 2017 et 2019, un poste extremement influent. Elle est aujourdhui <b>directrice de lInstitut francais en Algerie</ b>, ou Total porte des projets controverses.",
    "<b>Madji Abed</b> <br /><br />Le directeur delegue pour les relations institutionnelles internationales de Total depuis 2019 a ete recrute directement apres vingt ans au sein du <b>ministere des Affaires etrangeres </b>. Il a notamment ete consul general a Dubai, organisateur dun sommet entre les pays du pourtour mediterraneen, ou encore chef adjoint de mission au Qatar.",
    "<b>Romaric Roignan</b> <br /><br />Le directeur de la Strategie climat de Total a realise deux aller-retours entre Total et la haute-administration francaise. Entre 2009 et 2015, il a ete conseiller a lambassade des Etats-Unis, <b>conseiller affaires etrangeres du Premier ministre </b>, puis <b>directeur du cabinet de la secretaire dEtat au developpe- ment et a la francophonie</b>, avant dentrer un mois plus tard chez Total comme negociateur affaires nouvelles, puis comme directeur de projet dans le secteur de lexploration et de la production en Birmanie. Il est ensuite devenu en 2022 directeur de la strategie climat du groupe. Deja, en 2006, apres huit ans au ministere des Affaires etrangeres, Romaric Roignan avait rejoint Total pour trois ans comme adjoint au directeur des relations internationales.",
    "<b>Jean-Marie Chouzenoux</b> <br /><br />Jean-Marie Chouzenoux a ete collaborateur parlementaire avant de travailler a la DGSE de 1994 a 2004, puis de devenir <b>chef de cabinet de Renaud Muse- lier aux Affaires Etrangeres </b>, et de Francois Baroin a l<b>Outre-Mer</b>. Il est embauche par Total en 2008, au moment ou son reseau au sein de lEtat est le plus developpe, et opere encore aujourdhui comme directeur delegue des relations publiques.",
    "<b>Gabrielle Gauthey</b> <br /><br />La representante du PDG de Total aupres des institutions europeennes est une ancienne de la <b>Caisse des Depots et Consignations</b>, institution finan- ciere publique au vaste champ daction, dont elle etait encore directrice de linvestissement et du developpement local au sein du comite executif entre 2015 et 2021, en parallele de ses fonctions dans le prive notamment chez Total a partir de 2019.",
    "<b>Philippe Baptiste</b> <br /><br />Apres avoir pantoufle chez Total en 2016, Philippe Baptiste est revenu dans le public en 2017 et a compte a partir de la onze nominations au Journal officiel, a des postes fautement inflents. En effet, apres une carriere academique comme chercheur, Philippe Baptiste a ete de 2014 a 2016 directeur general delegue a la science au <b>CNRS</b>, puis en 2016 il est embauche directement par <b>Total en tant que directeur scientifique et vice-president du developpement scientifique</b>. Apres y avoir travaille de fevrier 2016 a mai 2017, il retourne dans le public pour occuper le poste de <b>directeur de cabinet de Frederique Vidal, ministre de lEnseignement superieur, de la Recherche et de lInnovation</b>. En 2019, il devient meme chef de pole education au sein du cabinet du Premier mi- nistre Edouard Philippe. Il est aujourdhui a la tete du <b>Centre National detudes Spatiales</b>.",
    "<b>Marie-Noelle Semeria</b> <br /><br />La directrice R&D de Total est une ancienne cadre du <b>CNRS</b>, qui dispose de connexions dans le secteur public et participe encore a la gouvernance de lAgence Nationale de Recherche, une structure publique dont elle est membre du conseil dadministration. Entree en 2014 au conseil dadministration du CNRS, Marie-Noel Semeria a celebre a loccasion de louverture dun centre de recherches mis en avant par le CNRS en novembre 2021 a Lille la force du lien entre le CNRS et Total :  Nous avons une relation de confiance de longue date avec le CNRS qui est un partenaire strategique de notre croissance dans les energies nouvelles et sur les thematiques du developpement durable . Elle a par ailleurs ete membre durant 4 ans du <b>Comite national de la recherche scientifique </b> et du Conseil superieur de la recherche et de la technologie, preside par le ministre de lEnseignement superieur, de la Recherche et de lIn- novation, et chief scientist du Commissariat a lEnergie Atomique et aux energies alternatives de 2012 a 2014.",
    "<b>Cathie Vix-Guterl</b> <br /><br />Cathie Vix Guterl a ete embauchee par Total en 2018 comme directrice de lanticipation strategique de la branche R&D du groupe apres une carriere entiere au <b>CNRS</b>. Malgre ce pantouflage, elle est nommee en 2020 au <b>conseil scientifique du Commissariat a lenergie atomique et aux energies alternatives </b>, non pas en tant que personnalite exterieure mais en tant que representante de lEtat.",
    "<b>Denis Favier</b> <br /><br />Denis Favier est souvent pris en exemple par les medias sinteressant aux pratiques de lobbying pour illustrer la capacite de Total a recruter des gros pois- sons, des haut-fonctionnaires reconnus pour avoir occupe des postes centraux dans ladministration. En effet, lorsque Total le recrute en 2016, Denis Favier arrive comme ancien <b>Directeur General de la Gendarmerie Nationale</b>. Dans son nouveau role de Directeur de la Securite chez Total, lancien haut-fonc- tionnaire, egalement ancien patron du <b>GIGN</b>, beneficie de son reseau et des informations auxquelles il avait pu avoir acces.",
    "<b>Julien Pouget</b> <br /><br />Apres un premier pantoufflage chez Alstom, cet ancien de lAgence des Participations de lEtat est devenu <b>conseiller</b> industrie puis economie du <b>President de la Republique</b> entre 2014 et 2016. Familier de tous les dossiers lies a lindustrie francaise et a leur gouvernance par lEtat, Julien Pouget est directement recrute par Total Renewables en 2016, ou il a progressivement gravi les echelons jusqua prendre la tete de la branche Asie de lentreprise.",
    "<b>Simon Desindes</b> <br /><br />Simon Desindes a ete recrute en juillet 2022 par Total comme directeur de projets juste apres son passage comme <b>conseiller pour les affaires industrielles aupres du ministre de la Defense</b>. Il avait egalement officie auparavant a la <b>Direction Generale de lArmement</b> et a lAgence des Participations de lEtat dans des secteurs lies notamment a la production et lapprovisionnement en carburants.",
    "<b>Denis Giorno</b> <br /><br />Denis Giorno a travaille pendant plus de vingt ans chez Total, notamment en tant que President Total Gaz et Nouvelles energies, puis a ete choisi en 2018 pour renegocier pour lEtat les contrats de six champs eoliens offshores en tant que <b>charge de mission au Ministere de la Transition Ecologique</b>, alors quil navait jamais travaille au sein de la fonction publique. Une fois sa mission de plusieurs mois terminee, Denis Giorno est retourne dans le prive travailler comme consultant a son compte.",
    "<b>Cecile Georges</b> <br /><br />Apres avoir passe dix ans a la <b>Commission de Regulation de lEnergie</b> de 2004 a 2014, puis six ans a la <b>Commission europeenne</b> tout en sie- geant au college de lAutorite de Regulation des Transports, Cecile Georges a rejoint en tant que Deleguee Generale en 2022 l<b>Union francaise de lelectri- cite</b>, lobby representant plus de 500 entreprises et dont Total Energie Gaz est lun des principaux clients. Cecile Georges fait egalement partie du Corps des Mines.",
    "<b>Institutions publiques</b> <br /><br />Total cherche a peser sur les institutions publiques, qui font la loi et controlent le respect par les entreprises de celle-ci. Pour ce faire, le groupe suit une logique dinfiltration : il se positionne comme expert afin de donner directement son avis au sein des institutions publiques, et y emploie des relais acquis a sa cause.",
    "<b>LAutorite des Marches Financiers</b> <br /><br />LAMF est un exemple de la strategie dinfiltration directe par la firme des autorites censees la reguler. Le directeur juridique du groupe Total, Adrien Hamelle, a ete nomme en 2021 par le Ministre de lEconomie et des Finances, Bruno le Maire, a la Commission des sanctions de lAMF, lautorite en charge de la regula- tion financiere en France. Total a donc obtenu la nomination par Bercy de son directeur juridique au sein de lautorite chargee de reguler ses operations. Total pourrait pourtant etre vise par lAMF : cest ce que demandaient en 2020 les associations Notre Affaire a Tous et Sherpa en montrant que Total sous-evaluait les risques financiers lies a sa dependance aux hydrocarbures. LAMF na pas fourni de reponse a ce signalement.",
    "<b>Deputee Les Republicains</b> <br /><br />En 2017, une loi a ete adoptee instaurant un devoir de vigilance pour les grandes entreprises. La loi ne precisait pas que cetait le tribunal judiciaire qui etait charge des litiges concernant ce devoir de vigilance, alors plusieurs grandes entreprises ont argue que le tribunal de commerce (plus porte sur la protection des entreprises) devait etre competent. En 2021, le projet de loi climat et resilience prevoyait de trancher en faveur du tribunal judiciaire, une solution majori- tairement approuvee. Pourtant, au dernier moment en commission mixte paritaire, la mention du texte de loi tranchant en faveur de la competence du tribu- nal judiciaire a ete retiree. Cette sequence sest reproduite a loccasion dun autre projet de loi depuis, ou une deputee Les Republicains est parvenue a faire introduire un amendement retirant la disposition. Cette deputee Les Republicains est par ailleurs actionnaire de Total.",
    "<b>European Financial Reporting Advisory Group</b> <br /><br />En 2019, TotalEnergies a participe a la premiere Task Force mise en place par le Reporting Lab de lEFRAG sur les Climate-related disclosures, cest-a-dire les informations liees a leur activite et au climat que les entreprises doivent reporter. Par sa participation au groupe de travail de linstitution europeenne chargee detablir les pratiques de reporting liees au climat, Total peut ainsi contribuer a definir les pratiques qui lui seront imposees.",
    "<b>Organisation Maritime Internationale</b> <br /><br />Une strategie deployee par Total consiste a envoyer ses experts au sein des institutions publiques faisant autorite : lOrganisation Maritime Internationale est par exemple lagence des Nations Unies referente dans son secteur. En 2015, Mediapart avait revele quun expert de Total avait ete choisi comme expert dans cette structure pour etudier la mise en place de carburants maritimes moins polluants, alors meme que le groupe est un producteur majeur de ces carburants.",
    "<b>Ambassades de France</b> <br /><br />Le projet EACOP a montre la capacite de Total a obtenir un soutien unique de la part des Ambassades. En 2019, Emmanuel Macron a nomme Jules-Armand Aniambossou ambassadeur de France en Ouganda : cet ancien camarade de promotion a lENA du President a notamment travaille chez Poweo, fournisseur denergie depuis rachete par Total. Ouvertement proche du groupe, lambassadeur a expressement soutenu le projet EACOP en 2019. Total entretient cette connexion par des rapports personnels mais aussi en financant par exemple la semaine de lamitie France-Ouganda comme sponsor platinum. Cette proxi- mite facilite les contacts de Total avec les autorites publiques locales. Ainsi, en Ouganda, Total opere directement en partenariat avec la police locale pour proteger ses zones dexploitation, notamment des militants locaux.",
    "<b>Stratgie d'optimisation fiscale</b> <br /><br />Malgre des benefices uniques pour une entreprise francaise de plus de 19,5 milliards deuros en 2022, Total paie moins dimpots quelle ne recoit de largent de lEtat. La technique employee par Total est simple : ses activites beneficiaires sont declarees la ou elles sont realisees ou dans des pays avantageux. En France, Total declare en revanche des franges de son activite davantage deficitaires, comme le raffinage ou les stations-services, alors quelle y realise pour- tant 21% de son activite, quelle y entretient 35 % de ses effectifs, et quelle y detient 44 % de son capital social. En 2022 encore, Total realise plus de onze milliards deuros de benefices sans payer le moindre impot sur les societes.",
    "<b>Bermudes</b> <br /><br />Les Paradise Papers ont revele en 2017 la localisation de nombreuses filiales de Total aux Bermudes, un paradis fiscal. Par exemple, les activites de Total aux Emirats-Arabes-Unis etaient toutes declarees aux Bermudes via trois societes : la Total Holding Dolphin Amont Limited et Total E&P Dolphin Upstream pour la partie extraction et production, et la Total E&P Dolphin Midstream pour la partie raffinage, transport et vente. A la suite des accusations de pratiques fiscales abusives, Total a rapatrie la majeure partie de ses filiales immatriculees aux Bermudes. Sept societes, pour lesquelles Total nest pas majoritaire mais exerce une activite de controle les apparentant a leur filiale, demeurent toutefois immatriculees aux Bermudes selon le rapport annuel de lentreprise paru en 2022. Par exemple, la localisation aux Bermudes de la societe Pars LNG Limited operant en Iran, qui avait ete identifiee par les Panama Pappers, na pas change aujourdhui.",
    "<b>Pays-Bas</b> <br /><br />Total immatricule 48 filiales aux Pays-Bas alors quelles noperent pas dans ce pays, afin de beneficier du regime fiscal tres avantageux de ce pays au sein de lUnion europeenne.",
    "<b>Administration fiscale francaise</b> <br /><br />Total a publie par elle-meme un rapport sur sa politique fiscale en 2021, revelant quelle na pas paye dimpot sur les societes en 2020, alors meme que le groupe a realise 11,4 milliards de benefices sur lannee 2019, a laquelle cet impot se rapporte. De fait, Total a moins paye dimpot sur les societes quelle na recu dargent de lEtat au cours de la decennie 2010. En 2015 deja, Patrick Pouyanne avait reconnu que le groupe navait paye aucun impot sur les societes entre 2012 et 2015, alors quil a realise 31,4 milliards deuros de benefices sur la periode, et quil setait pourtant permis en 2012 de se targuer dun engage- ment a ne pas creer dentites dans des pays dereglementes.",
    "<b>Initiative pour la Transparence dans les Industries Extractives</b> <br /><br />Total se targue de sengager pour la transparence fiscale alors meme quelle parvient a ne pas payer dimpot sur les societes en France. En effet, Total met en avant dans sa communication son engagement dans lInitiative pour la Transparence dans les Industries Extractives depuis sa creation en 2003, par laquelle elle soutient pleinement les initiatives pour plus de transparence et de responsabilite fiscale, mais place par exemple 48 de ses filiales aux Pays-Bas alors quelles ny exercent pour la plupart aucune activite.",
    "<b>Lobbies</b> <br /><br />Total est lentreprise francaise ayant depense le plus dargent en lobbying en 2021 selon la Haute Autorite de la Transparence de la Vie Publique. Total pro- meut ses interets de deux manieres : en se regroupant avec les entreprises de son secteur pour faire prevaloir leurs interets communs, et en se payant les services dentreprises de lobbying specialisees. La force de frappe de Total en matiere de lobbying est impressionnante, en France mais aussi en Europe et dans le monde via certaines organisations americaines dont les positions sur le climat sont plus que douteuses. Lors de la COP27 en 2022, les ONG ont re- cense 636 lobbyistes qui defendaient le charbon, le petrole et le gaz, un chiffre bien superieur a celui des activistes defendant lenvironnement et en hausse de 25% depuis lannee precedente.",
    "<b>Union Francaise des Industries Petrolieres</b> <br /><br />LUnion Francaise des Industries Petrolieres est le principal lobby de lenergie fossile en France, par lequel ont transite une partie significative des 2,51 mil- lions deuros de depenses de representation dinteret par Total. Ce total, le plus eleve en France devant Engie - 2,25 millions deuros - et le Credit Agricole -2,17 millions deuros-) a servi notamment via lUFIP une trentaine dactions de representation dinterets pour lannee 2021 concernant notamment le Code lenvironnement, les projets de loi de Finances, le Plan de relance ou encore le Code de lenergie.",
    "<b>IOGP</b> <br /><br />LInternational Association of Oil & Gas Producers est la principale association de representation des majors petrolieres, qui defend agressivement leurs in- terets. LIOGP chapeaute un systeme de representation organise : Total est aussi liee a lAmerican Chemistry council, a la Texas Oil & Gas Association et a lAmerican Gas Association, qui regroupent les majors americaines de maniere geographique et sectorielle aux Etats-Unis, ou encore a PlasticsEurope, Fuel- sEurope, Eurogas ou encore HydrogenEurope, les regroupements sectoriels europeens.",
    "<b>American Petroleum Institute</b> <br /><br />Total a longtemps fait partie de lAmerican Petroleum Institute, association de representation des majors americaines connue pour son lobbying contre la transition ecologique et notamment pour un scandale de desinformation massive sur le changement climatique. En 2019 et 2020, les positions de lAmerican Petroleum Institute (API) avaient ete evaluees par Total comme  partiellement alignees  avec ses positions. En 2021, une enquete du Guardian a revele que lAPI recevait dimportants financements des majors (dix millions de la part de Shell en 2020 par exemple) pour faire reculer la legislation et etait utilisee comme bouc emissaire par les majors pour essuyer a leur place les critiques publiques et politiques. Lenquete du Guardian rappelle que lAPI a par exemple finance en 1998 une campagne de desinformation massive a la suite de la signature du protocole de Kyoto, afin de remettre en cause lorigine humaine du changement climatique, dont le plan a ete revele. A partir de 2020, des actions judiciaires (Hoboken, New Jersey vs API) ont enterine le fait que lAPI financait directement des scientifiques niant ou reduisant le changement climatique. Total na pris ses distances avec lAmerican Petroleum Association quen 2021. De meme, avant 2021, Total participait a lAFPM (American Fuels & Petrochemicals Manufacturers) et la CAPP (Canadian Association of Petroleum Produ- cers). Les premieres actions juridiques contre lAPI devraient aboutir en 2023.",
    "<b>Syndicat des Energies Renouvelables</b> <br /><br />Total a progressivement infiltre les structures de representation des acteurs de lenergie renouvelable afin de maitriser les structures qui auraient pu soppo- ser a elle. Total a ainsi integre en France le Syndicat des Energies Renouvelables, dont Patrick Pouyanne sest rendu pour la premiere fois au grand colloque annuel en 2017. En Europe, Total utilise lorganisation Sun Power Europe, qui lui a par exemple permis dobtenir deux sieges sur huit au conseil dadminis- tration de lAssociation europeenne de lindustrie photovoltaique (EPIA), principale representante de la filiere solaire, et un siege a lAssociation europeenne de lenergie eolienne (EWEA). En 2015 deja, le Guardian avait denonce la prise de pouvoir de lindustrie fossile sur les associations de representation des energies renouvelables, montrant notamment la reduction de lobjectif affiche par ces associations de - 45% a - 30% demissions dici 2030 apres que Total, lespagnole Iberdrola ou encore litalienne Enel se soient mobilisees.",
    "<b>Association Francaise des Entreprises Privees</b> <br /><br />LAFEP est le lobby des plus grands groupes francais, notamment du CAC 40, que les grands patrons utilisent pour peser de leur poids commun sur les regle- mentations nationales et y promouvoir leurs interets partages.",
    "<b>Business Europe</b> <br /><br />Business Europe est le lobby principal des grandes entreprises en Europe, souvent critique pour operer a lencontre des initiatives europeennes en faveur de politiques climatiques plus ambitieuses. Business Europe fonctionne comme une structure chapeau des organisations de representation patronales natio- nales, comme le Medef en France. Total, tout comme BP, ExxonMobil ou Shell, fait partie du Corporate Advisory and Support Group de BusinessEurope. Bu- siness Europe contribue a lemprise des lobbies fossiles sur lUnion europeenne selon un rapport conduit par un ensemble dONGs, montrant notamment que 200 lobbyistes auto-declares au Registre europeen de transparence sont employes par le top 5 des majors petrolieres et leurs groupes de pression.",
    "<b>Lassociation professionnelle de lindustrie petrochimique</b> <br /><br />Le CEFIC est la principale association professionnelle de lindustrie chimique europeenne et egalement le lobby bruxellois dote du plus gros budget. Total est membre du CEFIC aux cotes de toutes les supermajors chimiques et petrolieres. Lassociation est connue pour jouer un role clef dopposition a toute poli- tique climatique ambitieuse de lUnion Europeenne et a notamment uvre au le sabotage de la loi sur les perturbateurs endocriniens. Egalement sponsor du Sommet Entreprises et Climat qui sest tenu en amont de la COP21, ce lobby ultra puissant a essaye de diluer toute reference aux etudes scientifiques sur le climat. En 2018, lassociation depense environ 12 millions deuros en lobbying et touche 131 063 de subventions. Elle est composee de 78 employes dont 49 equivalents temps plein, 24 accreditations pour aller et venir a leur guise dans les institutions europeennes. Elle a plus de 80 rendez-vous officiels avec la commission europeenne.",
    "<b>GasNaturally</b> <br /><br />Total a progressivement infiltre les structures de representation des acteurs de lenergie renouvelable afin de maitriser les structures qui auraient pu soppo- ser a elle. Total a ainsi integre en France le Syndicat des Energies Renouvelables, dont Patrick Pouyanne sest rendu pour la premiere fois au grand colloque annuel en 2017. En Europe, Total utilise lorganisation Sun Power Europe, qui lui a par exemple permis dobtenir deux sieges sur huit au conseil dadminis- tration de lAssociation europeenne de lindustrie photovoltaique (EPIA), principale representante de la filiere solaire, et un siege a lAssociation europeenne de lenergie eolienne (EWEA). En 2015 deja, le Guardian avait denonce la prise de pouvoir de lindustrie fossile sur les associations de representation des energies renouvelables, montrant notamment la reduction de lobjectif affiche par ces associations de - 45% a - 30% demissions dici 2030 apres que Total, lespagnole Iberdrola ou encore litalienne Enel se soient mobilisees.",
    "<b>Principaux projets controverss</b> <br /><br />Total opere dans plus de 130 pays par le biais de 1 140 filiales : le groupe ne dispose plus que de 5 raffineries en France et 70% de ses activites se situent en Asie et au Proche-Orient. Le groupe observe une presence globale, cultive un lien etroit avec lEtat francais, negocie avec les gouvernements. De nombreux projets menes par Total sont controverses car au cur de conflits politiques, parfois armes, graves. Ils menacent parfois de maniere significative les droits humains.",
    "<b>Ouganda et Tanzanie - EA- COP et Tilenga</b> <br /><br />En Ouganda et en Tanzanie, Total construit le plus grand oleoduc chauffe au monde (1 443km) pour extraire pas moins dun milliard de barils de petrole. Le demarrage de la production est prevu en 2025 pour une duree de vingt-cinq a trente annees. EACOP -East African Crude Oil Pipeline- est detenu a 62% par TotalEnergies, 15 % par la compagnie petroliere ougandaise, 15% par la tanzanienne et a 8 % par la societe chinoise Cnooc, chargee dun site mineur de pro- duction. Il traversera de nombreuses reserves et aires protegees, il menace dextinctions des especes classees sur la liste rouge de lUnion internationale pour la conservation de la nature (UICN) et menace de contamination les deux plus grandes reserves deau douce dAfrique de lEst, les lacs Victoria et Albert, mettant ainsi en peril lacces a leau potable et a la nourriture pour plus de 40 millions de personnes.",
    "<b>Russie</b> <br /><br />Fin 2021, TotalEnergies detenait 21% de ses reserves prouvees et realisait 18% de sa production de petrole et de gaz en Russie - la firme a ete accusee au cours de lannee 2022 de chercher a poursuivre son action sur ce pays en guerre avec lUkraine. En Russie, la production de petrole et de gaz naturel par Total provenait alors principalement de ses participations dans les champs Termokarstovoye, Kharyaga, ou encore Ternefte. Total detenait en outre des participa- tions significatives dans la principale societe independante russe dans le secteur du gaz PAO Novatek (19,4% au 31 decembre 2021). Total participait enfin aux megaprojets gaziers Yamal LNG (20%) et Arctic LNG 2 (10%), batis pour renforcer lextraction dhydrocarbures du sol russe et leur exportation notamment en Europe. Ce sont des menaces directes pour le climat et pour les ecosystemes polaires deja en premiere ligne face au changement climatique. Par ailleurs, a la suite de linvasion de lUkraine par la Russie le 24 fevrier 2022, Total a ete la seule major petroliere occidentale a maintenir ses activites en Russie et a ete accusee de contribuer directement par ses activites a lapprovisionnement en combustibles de larmee russe, dont les crimes de guerre sont averes. Des revelations du journal LeMonde ont ainsi montre que les hydrocarbures exploites par TernefteGaz en Siberie, dont Total detenait jusqua aout 2022 49% des parts, ont alimente en kerosene des escouades daviation russe responsables de bombardement de populations civiles. Total a maintenu jusquen decembre 2022 ses participations dans la societe gaziere russe Novatek.",
    "<b>Mozambique</b> <br /><br />En 2010, lune des plus grandes reserves de gaz naturel (energie fossile) dAfrique subsaharienne est decouverte au Mozambique, faisant rapidement naitre trois mega-projets, dont Mozambique LNG opere par Total depuis 2019. En 2022, Les Amis de la Terre publient un rapport qui montre comment le soutien pu- blic aux projets gaziers alimente la catastrophe humanitaire et climatique au Mozambique. Des liens etroits entre lindustrie fossile et le ministere des affaires etrangeres sont reveles. Les pratiques de portes tournantes sont courantes : par exemple, Jean-Claude Mallet, conseiller special de Jean-Yves Le Drian au ministere des Affaires etrangeres, a ete recrute par Total en mai 2019 pour occuper le poste de directeur des affaires publiques. En fevrier 2020, seulement deux jours apres que le gouvernement a annonce la fin des forages petroliers maritimes sur le territoire francais au nom de lurgence climatique, Jean-Yves Le Drian, ministre des Affaires Etrangeres, fait le deplacement au Mozambique pour defendre les interets de lindustrie fossile francaise. En 2021, TotalEnergies annonce la suspension du projet gazier pour force majeure a cause du conflit arme. Le versement des compensations dues aux locaux ayant ete deplaces pour faire place aux projets gaziers a cesse depuis cette date.",
    "<b>Ymen</b> <br /><br />Un site de Total a ete utilise comme prison au Yemen selon des informations recoupees par le journal Le Monde. Entre 2017 et 2019, des temoignages recueil- lis par lorganisation Amnesty International, par le panel dexperts sur le Yemen des Nations unies ainsi que par des ONG et des activistes yemenites ont fait etat de lexistence dun lieu de detention a Balhaf, administre par les forces emiraties au sein dune base militaire. Celle-ci a ete amenagee par les Emirats a la mi-2017 sur une partie du site industriel de Total, requisitionnee a la demande officielle du gouvernement yemenite.",
    "<b>Algerie</b> <br /><br />Total fait partie des entreprises internationales collaborant avec la compagnie nationale de petrole algerienne Sonatrach, critiquee pour lexploitation a risque de gaz non conventionnel en Algerie. Ces gisements de gaz non conventionnel incluent le gaz de schiste et le tight gas, qui necessitent lun comme lautre des techniques de fracturation hydraulique. Celle-ci implique des risques severes de pollution des nappes phreatiques par le cocktail de produits chimiques in- jectes pour fracturer la roche contenant le gaz, ainsi quune consommation deau extremement importante dans une region particulierement aride de lAlgerie ainsi que des risques de tremblement de terre. Alors que la fracturation hydraulique est interdite en France depuis 2011 (en depit de pressions de Total pour pouvoir exploiter des gaz de schiste dans la region de Montelimar), Total detient de 2009 a 2014 une part de 49% dans lexploration et lexploitation des gaz de schiste dAhnet, au sud de lAlgerie, gisement particulierement controverse en raison des risques quil fait peser sur la population de la ville environnante dIn Salah. Si Total a du y renoncer officiellement en labsence daccord economique trouve avec Sonatrach, lentreprise exploite depuis 2018 le gisement de tight gas de Timimoun par fracturation hydraulique, en depit des risques de cette technique et de la vive opposition des populations algeriennes affectees a lutilisation du fracking. A son annonce, le directeur exploration-production de Total se felicite que la mise en production de Timimoun constitue une nouvelle etape de lhistoire du Groupe en Algerie  cette nouvelle etape est dautant plus inquietante que dapres lAIE lAlgerie possede les troisieme reserves de gaz non conventionnel les plus importantes au monde, une bombe climatique aux conditions dexploitation particulierement dangereuses.",
    "<b>Argentine</b> <br /><br />Total a recours en Argentine a des pratiques dexploitation de gaz non conventionnel par fracturation hydraulique, presentant des effets particulierement nefastes pour les populations locales et les ressources en eau de la region, comme en Algerie. Total fait partie des principales compagnies petrolieres inter- nationales impliquees dans lexploitation de limmense reservoir de gaz et de petrole de schiste du Vaca Muerta depuis 2017. Ce site presente 336 puits de fracturation hydraulique, qui consomment de tres larges quantites deau et de produits chimiques dans une region particulierement aride de lArgentine (23 milliards de litres deau douce et 73,2 millions de litres dadditifs auraient ete consommes entre 2010 et 2020 pour exploiter le gaz de schiste de Vaca Muerta). Une enquete de Greenpeace a par ailleurs revele que Total ne respectait pas les preconisations pour le traitement des additifs chimiques utilises dans la frac- turation hydraulique en Argentine et deversait par lintermediaire de son sous-traitant argentin Trater des dechets et residus chimiques dans des decharges a ciel ouvert au nord de la Patagonie, a proximite dhabitations et sur les terres des populations Mapuche. Lexploitation du gaz et du petrole de schiste du Vaca Muerta par Total et dautres compagnies fossiles cause enfin un redoublement de lactivite sismique dans la region, constate par les populations locales et les ONG sur le terrain, rendant plus difficiles les conditions de vie des populations locales.",
    "<b>Republique du Congo</b> <br /><br />Total jouit dune implantation de longue date au Congo-Brazzaville et est associee a differentes periodes de son histoire a des scandales financiers et des vio- lations de droits humains dans ce pays. A la fin des annees 1990, la filiale congolaise dElf Aquitaine (dont la fusion avec Total et Petrofina mene au groupe Total au tournant des annees 2000) a finance de maniere averee des armes pour les deux parties de la guerre civile qui oppose les partisans de Denis Sassou Nguesso a ceux de Pascal Lissouba. Plus recemment, il a ete revele dans le cadre des Panama Papers que Total avait orchestre un montage financier pour permettre a la Republique du Congo de se soustraire a ses obligations aupres du FMI en echange de droits dans une concession petroliere dans les annees 2000.",
    "<b>Birmanie</b> <br /><br />Total a apporte entre 1992 et 2021 un soutien financier indirect a la junte militaire au pouvoir en Birmanie via lexploitation du gisement gazier de Yadana avec lentreprise birmane MOGE (Myanmar Oil and Gas Enterprise). Les revenus tires par la junte militaire de lexploitation de ce gisement par Total selevaient dans les annees 2000 a 450 millions de dollars, soit lune des principales sources de revenus de cette junte coupable de nombreuses exactions et de viola- tions des droits humains en Birmanie. En plus de contribuer au financement de la junte militaire, Total aurait eu recours au travail force par lintermediaire de larmee birmane dans le cadre de la construction dun gazoduc reliant le gisement gazier de Yadana a la Thailande. Huit Birmans ayant subi un travail force ont ainsi attaque Total pour sequestration au debut des annees 2000. La construction de ce pipeline a mene a des relocalisations forcees de villages entiers selon les rapports dONG presentes dans la region. En parallele, Total a ete attaquee en Belgique pour complicite de crimes contre lhumanite en Birmanie par quatre refugies birmans en 2002. Si laffaire a ete classee en 2008 en raison de linadequation de la loi belge sur laquelle etait fondee la procedure, de nom- breuses ONGs ont denonce la complicite de Total dans les crimes contre lhumanite commis en Birmanie. Apres des decennies de defense de sa presence en Birmanie et de financement indirect de la junte militaire, Total na accepte de se retirer du pays quen 2021. Par ailleurs, selon Multinationales.org, Total a fait pression pour etouffer la situation, par exemple en menacant a cette periode le journal Le Monde darreter leur achat despaces publicitaires a titre de represailles contre des publications au sujet de laction du groupe en Birmanie.",
    "<b>Israel et le Liban</b> <br /><br />Total a joue un role dans laccord passe le 12 octobre 2022 entre Israel et le Liban sur la delimitation des zones maritimes. En effet, certaines zones concer- nees contiennent des champs sous-marins de gaz et Total est choisie pour exploiter le gaz present dans la zone de Cana, au profit du Liban tout en versant une compensation a Israel. Selon certains analystes, lattribution a Total de lexploitation de ce champ gazier a ete soutenue par la diplomatie francaise, car elle permet un retour de la presence de la France dans la region.",
    "<b>Nigeria</b> <br /><br />Total fait partie des entreprises multinationales exploitant du petrole et du gaz dans le delta du Niger depuis 1962. Cette exploitation a conduit a des violations des droits humains et a des pollutions environnementales particulierement importantes dans lEtat de Rivers au sud du Nigeria ou Total possede un permis dexploitation onshore depuis les annees 1960. Le peuple Egi, present dans la region, a vu ses conditions de vie degradees et son environnement pollue par lextraction de petrole et de gaz au sein des terres de la communaute. Il est notamment avere que Total a recours au torchage du gaz dans son exploitation sur le delta du Niger alors que cette technique est interdite au Niger depuis 1984. Cette methode consiste a bruler le gaz sechappant lors des forages petroliers et mene a des emissions additionnelles ainsi quune pollution de lair particulierement severe aboutissant a des pluies acides qui mettent en danger la vie des habitants. Par ailleurs, des accidents lies a lexploitation de petrole ont eu lieu en 2012, aboutissant a des eruptions de gaz a proximite des habitations de la communaute Egi, mettant en danger leur sante et leur faisant courir des risques importants dincendie. Total sest enfin illustre par une strategie daccapa- rement des terres de la communaute Egi basee sur une legislation nigeriane facilitant lexpropriation des paysans par les firmes petrolieres. Lensemble de ces violations des droits humains et de ces pollutions ont valu a Total le prix Pinocchio 2014, decerne par les Amis de la Terre pour denoncer les entreprises ayant commis les pires violations des droits et pollutions de lannee.",
    "<b>Nouveaux projets fossiles depuis 2021 </b> <br /><br />En 2021, pour la premiere fois, lAgence Internationale de lEnergie a laquelle Patrick Pouyanne aimait tant se referer auparavant preconise des mesures dras- tiques : renoncer des a present a tout nouveau projet dexploration petroliere ou gaziere. Dun coup, le PDG de Total considere que le rapport de lAIE nest pas une bible. Nous prenons 2021 pour annee de reference car, envers et contre tous les avis scientifiques, Total continue dinvestir massivement dans les energies fossiles. La raison est simple : nous avons besoin de ces revenus sortis des hydrocarbures donc nous nallons pas y renoncer, selon les propres mots de Patrick Pouyanne.",
    "<b>Etats-Unis</b> <br /><br />Total realise pres de 90 % de ses operations de financement a travers des banques americaines, et 30 % de son actionnariat provient des Etats-Unis, selon des informations recoupees par Le Monde Diplomatique. Total est egalement engage dans des chantiers en eaux profondes dans le golfe du Mexique, ainsi que dans la production de gaz de schiste au Texas et dans lOhio. Total a ainsi produit 33 millions de barils equivalent petrole en 2021 aux Etats-Unis. Lexploitation du gaz de schiste est controversee, de meme que lexploitation des sables bitumineux, extremement nocive pour les sites et particulierement emettrice",
    "<b>Canada</b> <br /><br />Total participe a lexploitation des sables bitumineux de lAlberta, lun des points chauds des emissions de gaz a effet de serre a lechelle mondiale. Lexploi- tation de cette ressource est controversee car lextraction dun baril de petrole des sables bitumineux de lAlberta emet plus du triple de dioxyde de carbone que la production dun baril de petrole classique, et car elle conduit au rejet dimportantes substances toxiques.",
    "<b>Inde</b> <br /><br />Total a acquis en 2021 un cinquieme du developpeur de projets renouvelables indien Adani pour 2 milliards de dollars, un achat de parts dans une societe existante que Total comptabilise au sein de ses investissements dans le renouvelable, et qui compte pour plus de la moitie de ces derniers cette annee-la.",
    "<b>Nigeria</b> <br /><br />Le Nigeria est un exemple des liens qui existent souvent entre Total et les autorites publiques locales. Total opere notamment en joint-venture avec la societe petroliere nationale du Nigeria, dont certains anciens dirigeants ont ete des ex-cadres de Total sur place, comme Chidi Momah, ex-directeur executif de Total Nigeria puis directeur juridique de la NNPC. Total est aussi actionnaire a hauteur de 15% de la societe Nigeria LNG. LAngola peut egalement etre prise en exemple : premier operateur du pays, Total est partenaire avec la societe nationale Sonangol et lAgence nationale du petrole, du gaz et des biocarburants pour des contrats dexploitation. Par exemple, en decembre 2019, lensemble des licences de production du bloc 17 local a ete prolonge jusquen 2045.",
    "<b>Iran</b> <br /><br />Total possede de longue date une filiale en Iran, Elf Petroleum Iran, a laquelle deux nouvelles filiales se sont additionnees depuis. En 2017, par la signature dun accord pour lexploitation de ressources en gaz, Total sest rapproche de cet acteur pourtant en tension avec les Etats-Unis. Dans sa politique de rela- tions internationales, Total soigne sa relation avec lIran tout en soignant son image mediatique : a la suite des evenements qui ont bouleverse le pays en 2021, Total a par exemple fourni des cartes carburants utilisables dans les stations-services de TotalEnergies a lambassade dIran a Paris.",
    "<b>Qatar</b> <br /><br />En plus de participer a la production et lexportation de gaz dans deux usines au Qatar, TotalEnergies et QatarEnergy collaborent activement sur de nombreux projets a linternational, en particulier en Amerique du Sud. En 2021, ils ont par exemple signe une alliance pour obtenir les droits dexploitation de deux blocs offshores avec la compagnie petroliere du Suriname, Staatsolie.",
    "<b>Suriname</b> <br /><br />En novembre 2021, TotalEnergies et le gouvernement du Suriname ont signe un accord de partenariat pour contribuer a la preservation des forets du pays comme puits de carbone. Pourtant, dans le meme temps, TotalEnergies mene des activites dexploration et vise a renforcer lexploitation des ressources fos- siles sur ce territoire. En 2021, TotalEnergies sest par exemple allie a QatarEnergy pour obtenir les droits dexploitation de deux blocs offshore.",
    "<b>Thailande</b> <br /><br />TotalEnergies possede pour principale participation dans ce pays un tiers dun important site offshore, dont la production est achetee en totalite par la com- pagnie nationale thailandaise PTT : sur ce site, plusieurs nouveaux puits ont ete fores en 2021.",
    "<b>Chine</b> <br /><br />Dans le site situe en Mongolie interieure ou se concentre lactivite de Total en Chine, le forage des puits de developpement de tight gas se poursuit.",
    "<b>Afrique du Sud</b> <br /><br />Total est en passe de lancer en Afrique du sud lexploitation dun gisement offshore dun milliard de barils, une bombe climatique denoncee par de nom- breuses ONG en octobre 2022. De fait, Total a depose le 5 septembre 2022 une demande de licence de production pour lun des cinq sites pour lesquelles elle dispose dun permis dexploration en offshore profond. Dans son ensemble, le projet demande trois milliards deuros dinvestissements pour forer les fonds sous-marins - autant que lensemble de linvestissement de Total dans les renouvelables en 2021. Il va directement a lencontre de la position degagee par les autorites internationales sur la necessite de maintenir toutes les energies fossiles dans le sol, met directement en danger la biodiversite dans une zone de migration de nombreux mammiferes marins et menace les conditions de vie des pecheurs locaux.",
    "<b>Namibie</b> <br /><br />TotalEnergies opere deux permis dexploration en offshore profond et a annonce en fevrier 2022 de nouvelles decouvertes, qui permettraient dextraire davan- tage de ressources fossiles.",
    "<b>Nigeria</b> <br /><br />TotalEnergies detient une participation de 15% dans la societe Nigeria LNG, dont les actionnaires ont approuve fin 2019 le lancement dun projet dinstallation dune capacite supplementaire de 7,6 Mt/an.",
    "<b>Cote dIvoire</b> <br /><br />TotalEnergies opere un permis dexploration sur un site offshore.",
    "<b>Sao Tome et Principe</b> <br /><br />TotalEnergies detient deux permis dexploration attribues en 2019.",
    "<b>Senegal</b> <br /><br />TotalEnergies poursuit les activites dexploration sur deux blocs offshore.",
    "<b>Kenya</b> <br /><br />Dans ce pays ou plusieurs decouvertes dhuile ont ete faites, TotalEnergies evalue les options possibles pour un eventuel developpement commercial.",
    "<b>Canada</b> <br /><br />TotalEnergies participe a des projets dexploitation de petrole de sables bitumineux dans la province de lAlberta.",
    "<b>Bresil</b> <br /><br />TotalEnergies detient des participations dans de nombreux sites au Bresil et y poursuit des operations de developpement de nouvelles infrastructures. Par exemple, TotalEnergies et ses partenaires QatarEnergy et Petronas ont remporte, lors des 16e encheres petrolieres de lANP en octobre 2019, lexploitation dun site offshore dans le bassin de Campos. Le groupe a egalement signe en 2021 un accord avec Petrobras pour evaluer le potentiel dexploration de nou- velles zones prometteuses au Bresil et developper de nouvelles technologies, en particulier dans loffshore profond.",
    "<b>Mexique</b> <br /><br />TotalEnergies detient des licences dans sept blocs dexploration offshore dans le golfe du Mexique.",
    "<b>Guyana</b> <br /><br />TotalEnergies detient, notamment avec QatarEnergy, des participations dans trois sites, dont pour deux dentre eux des nouveaux puits dexploration ont ete fores ou sont prevus (bloc Canje et bloc Kanuku).",
    "<b>Etats-Unis</b> <br /><br />Total detient des interets dans le projet dAnchor dans le Golfe du Mexique, dont la mise en production est prevue en 2024.",
    "<b>Malaisie</b> <br /><br />TotalEnergies detient des interets dans des permis dexploration avec un puits dexploration offshore dans la province de Sabah.",
    "<b>Australie</b> <br /><br />TotalEnergies a annonce en novembre 2021 deux partenariats, avec AgriProve pour developper des puits de carbone souterrains sur 20 000 hectares en Aus- tralie, et avec Corporate Carbon pour y contribuer a la prevention des feux de savane. Dans le meme temps, le groupe y renforce pourtant sa production de gaz naturel liquefie par les projets Gladstone (27,5%) et Ichthys (26%). Le projet Ichthys consiste par exemple a developper un champ de gaz a condensats dans le bassin marin de Browse, avec une plateforme pour la production, le traitement et lexport du gaz, un gazoduc de 889 km et une usine de liquefaction implantee a terre, a Darwin.",
    "<b>Papouasie-Nouvelle-Guinee</b> <br /><br />TotalEnergies detient plusieurs licences dexploration et progresse sur ce territoire : le groupe et ses partenaires ont par exemple signe un accord avec lEtat independant de Papouasie-Nouvelle-Guinee definissant le cadre fiscal pour le developpement du projet Papua LNG en avril 2019. Cet accord a ete complete par un accord de stabilite fiscale signe avec lEtat en fevrier 2021 et par un accord permettant de prolonger la licence de cinq ans.",
    "<b>Mauritanie</b> <br /><br />Total poursuit des operations de minage a des fins dexploration.",
    "<b>Oman</b> <br /><br />En Oman, TotalEnergies a signe en decembre 2021 un accord de concession pour exploiter un site de gaz offshore, vendre son gaz naturel au gouvernement dOman et conclure un pacte dactionnaire pour la societe Marsa LNG constituee en vue de produire du gaz naturel a partir du bloc 10.",
    "<b>Culture et patrimoine</b> <br /><br />Le mecenat de Total dans le domaine culturel est cible et strategique : le groupe finance de maniere reguliere les expositions des grands musees nationaux comme le Louvre, le musee du Quai Branly ou lInstitut du monde arabe qui sont liees a des enjeux diplomatiques strategiques pour la compagnie. Ainsi, Total financait en 2018 lexposition Le Louvre a Teheran au moment meme ou le groupe negociait avec lIran pour lexploitation dimmenses ressources gazieres. La Fondation Total est par ailleurs connue pour etre omnipresente dans cette institution culturelle, dont lancien PDG de Total Thierry Desmarest a longtemps ete administrateur sur nomination du ministere de la Culture. Des militants, au Royaume-Uni et en France notamment, ont commence a mettre en lumiere ces liens avec succes : des institutions renoncent aux financements fossiles, et dautres pourraient suivre.",
    "<b>Fondation du Patrimoine Notre Dame</b> <br /><br />20 000 000  en 2021.",
    "<b>Fondation Du Patrimoine</b> <br /><br />Total finance La Fondation du Patrimoine depuis 2006. Pour la periode 2021-2023, la donation du groupe seleve a pres de 1,6 million deuros.",
    "<b>Fondation Memoire De Lesclavage</b> <br /><br />380 000  en 2021.",
    "<b>La Cite de la Musique, Philharmonie de Paris</b> <br /><br />Patricia Barbizet est presidente du Conseil dAdministration de la Philharmonie de Paris et est membre du CA de Total. La Fondation Total a finance la Cite de la Musique a hauteur de 200 000  en 2021.",
    "<b>Linstitut Du Monde Arabe</b> <br /><br />Patrick Pouyanne, PDG de Total, siege au Conseil dAdministration de lInstitut du Monde Arabe depuis 2017. La Fondation Total a finance lInstitut du Monde Arabe a hauteur de 150 000 en 2021.",
    "<b>Le musee Du Louvre-Lens</b> <br /><br />Financement par la Fondation Total a hauteur de 150 000 . La Fondation Total a finance de nombreuses expositions au Louvre dans le passe : par exemple, elle a finance lexposition Le Louvre a Teheran au moment ou le groupe negociait avec lIran pour un projet geant dexploitation gaziere. Les contreparties des dons de Total lui permettent de pratiquer une diplomatie economique avec les pays dans lesquels le groupe cherche a developper de nouveaux projets (par exemple en les accueillant dans une salle pour une reception prestigieuse au Louvre). Ce lien a ete denonce par des actions du collectif Liberons le Louvre et de Greenpeace.",
    "<b>Linstitut Francais</b> <br /><br />Outil de la diplomatie culturelle francaise a letranger, lInstitut Francais a recu 200 000  de la FondationTotal en 2021.",
    "<b>Musee du Quai Branly</b> <br /><br />Depuis 2009, la fondation Total est un grand mecene du musee du Quai Branly. Elle a soutenu plus de dix expositions pour la plupart centrees sur les  arts africains  (Cote dIvoire, Nigeria, Congo), ainsi que sur  Les Arctiques  en 2010 et sur la Papouasie Nouvelle-Guinee en 2016. Total a evidemment des activites strategiques dans tous ces secteurs geographiques.",
    "<b>Bibliotheque Nationale de France</b> <br /><br />De 2016 a 2021, Total a soutenu le projet Bibliotheque dOrient de la BnF. Il sagit dune bibliotheque numerique collaborative trilingue (francais-arabe-an- glais) qui rassemble des documents issus des collections de la BnF et de huit bibliotheques au Proche-Orient.",

]
